import { Component, Build, h } from '@stencil/core';
import { common } from "../../../global/data/common";

@Component({
	tag: 'ks-page-footer',
	styleUrl: 'footer.css'
})
export class PageFooter {
	render() {
		const phone = common.get('phone');
		const email = common.get('email');
		const time = common.get('workingHours');
		const company = common.get('company');
		const address = common.get('address');
		const address2 = common.get('address2');
		const nip = common.get('nip');
		const regon = common.get('regon');
		const softwareLink = common.get('softwareLink');

		const strings = common.get('translations');

		return[
			<div class="about">
				<div class="links">
					{common.get('footerLinks').map(section =>
						<ks-footer-links heading={section.name}>
							{section.items.map(item =>
								<li><a href={item.link}>{item.name}</a></li>
							)}
						</ks-footer-links>
					)}

					<div class="contact">
						<p>{strings.footerContact}</p>
						
						<a style={{"display": "none"}}></a>

						<a href={`tel:${phone}`}>
							<ks-icon name="phone" aria-label="Nr telefonu"/>
							{phone}
						</a>

						<a href={`mailto:${email}`}>
							<ks-icon name="mail" aria-label="Email"/>
							{Build.isBrowser ? email : ""}
						</a>

						<p>
							<ks-icon name="clock" size={0.9} aria-label="Czas pracy"/> {time}
						</p>
						<p>
							<ks-icon name="home" size={0.9} aria-label="Firma"/> {company}
						</p>
						<p>
							<ks-icon name="map-pin" size={0.9} aria-label="Adres"/> {address}
						</p>
						{address2 && <p>
							<ks-icon name="map-pin" size={0.9} aria-label="Adres" /> {address2}
						</p>}
						{nip && <p>
							<ks-icon name="book" size={0.9}/>NIP: {nip}
						</p>}
						{regon && <p>
							<ks-icon name="book" size={0.9}/>REGON: {regon}
						</p>}
					</div>
				</div>

				<div class="newsletter">
					<div>{strings.footerNewsletterSmall}</div>
					<div>{strings.footerNewsletterLarge}</div>
					<ks-button light border name={strings.footerNewsletterSubmit}
						onClick={()=> document.querySelector('ks-newsletter-popup-edrone')?.Show()}
						ariaLabel={`${strings.footerNewsletterSmall} ${strings.footerNewsletterLarge}`}
					/>
				</div>
			</div>,
		
			<div class="portals">
				<div>
					{common.get('social').map(social =>
						<ks-footer-button width={social.width} height={social.height} href={social.link} image={social.image} name={social.name} />
					)}
				</div>

				<div>
					{common.get('reviewers').map(reviewer =>
						<ks-footer-button width={reviewer.width} height={reviewer.height} href={reviewer.link} image={reviewer.image} name={reviewer.name} />
					)}
				</div>
			</div>,
			<div class="software">
				<a href={softwareLink} rel="nofollow">{strings.footerSoftware}</a>
			</div>
		];
	}
}