ks-input-check {
    display: block;
    margin-bottom: 15px;
}

ks-input-check label {
    position: relative;
}

ks-input-check input {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    height: 16px;
    width: 16px;
    overflow: hidden;
    margin: 0px 8px 2px 0px;
    vertical-align: middle;
    appearance: none;
    outline-offset: 4px;

    background-color: #e7e7e7;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    border: 1px solid #ccc;

    box-sizing: border-box;
    border-radius: 0;
    font: inherit;

    transition: .2s ease-in-out;
    transition-property: background-color,border;
}

ks-input-check[invalid] input {
    border-color: var(--color-secondary-darker);
}

ks-input-check[invalid] input:focus {
    border-color: var(--color-secondary-darker);
}

ks-input-check input:checked {
    background-color: #222222;
    border-color: transparent;
}

ks-input-check input[type="radio"] {
    border-radius: 50%;
}


ks-input-check ks-icon {
    position: absolute;
    top: 4px;
    left: 0.5px;
    color: #e7e7e7;
}

@media (max-width: 420px) {
    ks-input-check ks-icon {
        top: 2px;
    }
}

ks-input-check input[type="radio"]:checked::after {
    content: "";
    background-color: white;

    margin-top: 0px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
}

ks-input-check[invalid] span {
    color: var(--color-secondary-darker);
}

ks-input-check[invalid] p {
    margin: 10px 0px 0px 0px;
    color: var(--color-secondary-darker);
    font-weight: 700;
}



ks-input-check[large] label {
    display: flex;
}

ks-input-check[large] label input {
    width: 32px;
    height: 32px;
    margin: 1px 12px 2px 0px;
}

ks-input-check[large] label span {
    width: 100%;
}

ks-input-check[large] ks-icon {
    top: 6px;
    left: 3px;
}


ks-input-check[large] input:checked::after {
    margin-top: 2px;
    font-size: 18px;
}

ks-input-check[large] input[type="radio"]:checked::after {
    margin-top: 0px;
    width: 16px;
    height: 16px;
}

ks-input-check[large] p {
    margin-left: 40px;
}