ks-product-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    
    width: 100%;
    text-align: center;

    background: var(--card-background);
    color: var(--card-text-color);
    box-shadow: var(--card-shadow);
}

ks-product-card[flat] {
    border: #ededed 1px solid;
    border-radius: 6px;
    box-shadow: none;
}

@media (min-width: 476px) {
    ks-product-card { width: calc(50% - 15px); }
}

@media (min-width: 640px) {
    ks-product-card {
        width: 228px;
        max-width: 280px;
    }

    ks-product-card .cart {
        font-size: 13px;
    }

    ks-product-card[squashed] .price .previous {
        margin-bottom: 0;
    }
}

ks-product-card .top {
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: space-evenly;

    padding: 15px 15px 5px 15px;
    position: relative;

    min-height: 200px;

    color: inherit !important;
    text-decoration: none !important;
    font-size: 14px;
}

ks-product-card .top ks-img3 {
    min-height: 140px;
    max-height: 200px;
    margin-top: 10px;
}

ks-product-card ks-img3 {
    height: auto;
    margin: 0 auto 5px auto;
    max-width: 280px;
    aspect-ratio: 1 / 1;
}

ks-product-card .price > * {
    display: block;
    font-family: var(--font-emphasis);
}

ks-product-card .price .previous {
    color: #888888;
    font-size: 15px
}

ks-product-card[squashed] .price .previous {
    margin-bottom: -6px;
}

ks-product-card .price .current {
    color: var(--color-secondary);
    font-weight: bold;
    font-size: 17px;
}


ks-product-card .lastPrice {
    margin: 7px 0 -4px 0;
    color: #171717;
    font-family: var(--font-regular);
}

ks-product-card .lastPrice div {
    margin-bottom: -3px;
    font-size: 13px;
}

ks-product-card .bottom {
    display: flex;
    margin-top: 10px;
}

ks-product-card .bottom .unavailable,
ks-product-card .bottom .link {
    display: block;
    width: 100%;

    padding: 10px 10px;
    font-size: .875rem;

    text-align: center;
    text-decoration: none;
    text-transform: none;

    color: white;
    background-color: var(--color-secondary);
    transition: var(--transition-background-color);
}

ks-product-card .bottom .unavailable {
    color: #252525;
    background-color: #f1f1f1;
}

ks-product-card .bottom .link:hover {
    background-color: var(--color-secondary-hover);
}

ks-product-card .bottom .link:active {
    background-color: var(--color-secondary-active);
}

ks-product-card[unavailable] .top,
ks-product-card[unavailable] .price {
    opacity: 0.6;
}

ks-product-card[unavailable] .price .current {
    color: #252525;
}

@media (max-width: 420px) {
    ks-product-card .top {
        font-size: 13px;
        padding: 8px;
    }

    ks-product-card .price {
        line-height: 18px;
    }
}


ks-product-card .cart {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    min-height: 42px;
    min-width: 44px;

    padding: 1px 10px;
    font-size: .875rem;
    line-height: 40px;

    text-align: center;
    text-decoration: none;
    text-transform: none;
    font-family: var(--font-regular);

    border: none;
    border-radius: 0px;

    color: white;
    background-color: var(--color-secondary);
    transition: var(--transition-background-color);

    border-radius: 0 0 6px 0;
}

ks-product-card .cart:hover {
    background-color: var(--color-secondary-hover);
}

ks-product-card .cart:active {
    background-color: var(--color-secondary-active);
}

ks-product-card .fav {
    position: relative;
    display: block;
    height: 100%;
    min-height: 42px;
    min-width: 44px;

    padding: 1px 9px;
    font-size: .875rem;
    line-height: 40px;

    text-align: center;
    text-decoration: none;
    text-transform: none;

    border: none;
    border-radius: 0px;

    color: white;
    background-color: var(--color-secondary-darker);
    transition: var(--transition-background-color);

    border-radius: 0 0 0 6px;
}

ks-product-card .fav:hover {
    background-color: var(--color-secondary-darker-hover);
}

ks-product-card .fav:active {
    background-color: var(--color-secondary-darker-active);
}

ks-product-card .fav .success {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    align-items: center;
    justify-content: center;

    background-color: var(--product-card-secondary);
    animation: fade-in 0.2s 1;
}

ks-product-card .shipping {
    color: white;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #228622;
    padding: 5px 10px;
    border-radius: 0 0 0 10px;
    font-size: 13px;
}

ks-product-card .shipping ks-icon {
    padding: 2px;
    background-color: white;
    border-radius: 15px;
    color: #228622;
}

ks-product-card .shipping ks-icon svg {
    stroke-width: 2;
    transform: translateY(0.5px);
}

ks-product-card .compare button {
    appearance: none;
    border: none;
    background-color: transparent;
    margin-top: 10px;
    cursor: pointer;

    transition: opacity 0.3s ease;
}

ks-product-card .compare button span {
    margin-right: 5px;
}

ks-product-card .compare button ks-icon {
    line-height: 20px;
}
ks-product-card .compare button ks-icon svg {
    width: 20px;
    height: 20px;
}

ks-product-card .compare button:hover {
    opacity: 0.6;
}

ks-product-card .compare button:active {
    opacity: 0.4;
}



ks-product-card[constheight] {
    display: flex;
    height: 400px;
    flex-direction: column;
}

ks-product-card[constheight] .top {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

ks-product-card[constheight] .top ks-img3 {
    height: 60%;
}