import { Component, h, Method, Element, Prop, Event, EventEmitter, Host } from '@stencil/core';

@Component({
	tag: 'ks-overlay',
	styleUrl: 'overlay.css'
})
export class Overlay {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) dark: boolean = false;
    @Prop({mutable: true}) close: boolean = true;
    @Prop({mutable: true, reflect: true}) active: boolean = false;

    @Event() closed: EventEmitter;

    timeout: number;
    timeoutLength: number = 300;

    dialogElement: HTMLDialogElement;
    

    componentDidLoad() {
        this.dialogElement = this.root.shadowRoot?.querySelector('dialog') || this.root.querySelector('dialog');
        
        if (this.dialogElement) {
            // Handle clicking outside the dialog (on the backdrop)
            this.dialogElement.addEventListener('click', (e) => {
                if (e.target === this.dialogElement && this.close) {
                    this.hide();
                }
            });

            // Handle close event from dialog
            this.dialogElement.addEventListener('close', () => {
                this.closed.emit();
                this.scrollon();
                this.active = false;
            });

            // Handle Escape key (built into dialog, but we want to emit our event)
            this.dialogElement.addEventListener('cancel', (e) => {
                if (!this.close) {
                    e.preventDefault(); // Prevent closing if close is disabled
                } else {
                    this.closed.emit();
                }
            });
        }
    }

    @Method() async show() {
        if (this.dialogElement) {
            this.dialogElement.showModal();
            this.active = true;
            this.scrolloff();
        }
    }

    @Method() async hide() {
        if (this.dialogElement)
            this.dialogElement.close();
    }

    scroll: number;
    scrollon() {
        if(document.body.style.position == "fixed") {
            document.body.style.position = "static";
            document.body.style.overflowY = "auto";
            document.body.style.width = "unset";
            
            window.scrollTo(0, this.scroll);
            return;
        }
    }

    scrolloff() {
        this.scroll = window.scrollY;
        document.body.style.position = "fixed";
        document.body.style.top = `${-this.scroll}px`;
        document.body.style.overflowY = "scroll";
        document.body.style.width = "100%";
    }

    keydown(event: KeyboardEvent) {
        if (event.key === 'Escape' && !this.close)
            event.preventDefault();
    }

	render() {
		return <Host>
            <dialog onKeyDown={e=>this.keydown(e)}>
                <slot />
            </dialog>
        </Host>
	}
}
