import { Component, h, State, Method, Element } from '@stencil/core';
import { common } from '../../../../global/data/common';

@Component({
    tag: 'ks-navbar-contact-panel',
    styleUrl: 'navbar-contact-panel.css'
})
export class NavbarContactPanel {
    @Element() root: HTMLElement;
    @State() initialized = false;

    @Method()
    async Toggle() {
        this.root.querySelector("ks-overlay").show()
        this.initialized = true;
    }

    async close() {
        this.root.querySelector("ks-overlay").hide()
    }

    render() {
        const address = common.get('address');
        const address2 = common.get('address2');
        const nip = common.get('nip');
        const regon = common.get('regon');
        const workingHours = common.get('workingHours');
        const mapAddress = encodeURIComponent(common.get('mapAddress'));

        return <ks-dialog dark>
            <div class="contents">
                <div class="address">
                    <div class="emphasis">{common.get('company')}</div>
                    {address && [address, <br/>]} 
                    {address2 && [address2, <br/>]} 
                    {nip && ["NIP: ", nip, <br/>]} 
                    {regon && ["REGON: ", regon, <br/>]} 
                    {workingHours && workingHours} 
                </div>

                <div class="buttons">
                    <ks-button round link={"tel:" + common.get('phone')} name={common.get('phone')} />
                    <ks-button round link={"mailto:" + common.get('email')} name={common.get('email')} />
                </div>

                {this.initialized ?
                    <iframe frameborder="0" aria-hidden tabindex={-1}
                        src={`https://maps.google.pl/maps?ie=UTF8&q=${mapAddress}&gl=PL&hl=pl&t=m&iwloc=A&output=embed`}
                    ></iframe>
                : null}
            </div>
        </ks-dialog>;
    }
}