ks-sidepanel  {
    display: block;
}

ks-sidepanel .content  {
    position: absolute;
    z-index: 100000;

    top: 0;
    right: 0;
    bottom: 0;
    box-sizing: border-box;
    
    width: 320px;
    height: 100%;
    padding: 15px 30px;
    
    overflow-y: auto;
    
    color: black;
    background: #ffffff;
}

ks-sidepanel[large] .content  {
    width: 400px
}

ks-sidepanel[left] .content  {
    left: 0;
}

ks-sidepanel .content .close {
    position: absolute;
    top: 15px;
    right: 25px;
    padding: 5px;
    color: #252525;

    transition: color .1s ease-in-out;
    cursor: pointer;
    border: none;
    background-color: transparent;
}

@media only screen and (max-width: 640px) {
    ks-sidepanel .content {
        width: 300px;
        padding: 20px;
    }

    ks-sidepanel[large] .content {
        width: 360px;
    }

    ks-sidepanel .content .close {
        right: 15px;
    }
}

@media only screen and (max-width: 400px) {
    ks-sidepanel .content {
        width: 100%;
    }

    ks-sidepanel[large] .content {
        width: 100%;
    }
}