import { Component, h, Prop, Element } from '@stencil/core';

@Component({
      tag: 'ks-button',
      styleUrl: 'button.css'
    })
export class Button {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) name: string;
    @Prop({reflect: true}) ariaLabel: string;
    @Prop({reflect: true}) pressed: boolean;
    @Prop({reflect: true}) icon: string;
    @Prop({reflect: true}) image: string;
        
    @Prop() submit: boolean;
    @Prop() link: string;
    @Prop() nofollow: boolean;
    @Prop() newtab: boolean;
    @Prop({reflect: true}) disabled: boolean;

    @Prop({reflect: true}) left: boolean;

    @Prop({reflect: true}) narrow: boolean;
    @Prop({reflect: true}) narrower: boolean;
    @Prop({reflect: true}) tall: boolean;
    @Prop({reflect: true}) round: boolean;
    @Prop({reflect: true}) rounded: boolean;
    @Prop({reflect: true}) primary: boolean;
    @Prop({reflect: true}) secondary: boolean;
    @Prop({reflect: true}) border: boolean;
    @Prop({reflect: true}) transparent: boolean;

    @Prop({reflect: true}) light: boolean;
    @Prop({reflect: true}) muted: boolean;

    @Prop({reflect: true}) loading: boolean;

    @Prop({reflect: true}) transitionless: boolean;

	render() {
        const content = [
            this.icon && this.left ? <ks-icon name={this.icon} size={0.9} aria-hidden="true"/> : null,
            this.name,
            this.icon && !this.left ? <ks-icon name={this.icon} size={0.9} aria-hidden="true"/> : null,
            this.image && <ks-img3 image={this.image} fit='contain' aria-hidden="true" sync/>,
            this.loading && <ks-loader dark={this.border || this.transparent} /> 
        ]

        if(this.link)
            return <a href={this.disabled ? undefined : this.link}
                aria-label={this.ariaLabel}
                rel={this.nofollow ? "nofollow" : null}
                target={this.newtab ? "_blank" : null}
            >{content}</a>;

        return <button type={this.submit ? "submit" : "button"}
            disabled={this.disabled || this.loading}
            aria-label={this.ariaLabel}
            aria-pressed={this.pressed ? "true" : "false"}
        >{content}</button>;
    }
}
