import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-footer-button',
  styleUrl: 'footer-button.css'
})
export class FooterButton {
	@Prop() name: string;
	@Prop() image: string;
	@Prop() href: string;

	@Prop() width: number = 120;
	@Prop() height: number = 50;

	render() {
		return(
			<a href={this.href} rel="nofollow" aria-label={this.name}>
				<ks-img2 vertical src={this.image} observerMargin="500px" alt={this.name} width={this.width} height={this.height}></ks-img2>
			</a>
		);
	}
}