import { Component, h, Method, Element, State } from '@stencil/core';

@Component({
	tag: 'ks-message-popup',
	styleUrl: 'message-popup.css'
})
export class MessagePopup {
    @Element() root: HTMLElement;
    overlay: HTMLKsOverlayElement;

    @State() name: string = "";
    @State() message: string = "";
    @State() link: string = "";
    @State() linkname: string = "";

    componentDidRender() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    @State() visible: string = "";

    @Method() async show(name: string, message: string, linkname: string = "", link: string = "") {
        this.name = name;
        this.message = message;
        this.linkname = linkname;
        this.link = link;

        this.overlay.show();
        this.visible = "visible";
    }

    @Method() async hide() {
        this.visible = "hidden";
        this.overlay.hide();
    }

	render() {
		return <ks-dialog>
            <div class="title">{this.name}</div>
            <div class="message" innerHTML={this.message}></div>
            {this.linkname && this.link ? 
                <ks-button secondary round link={this.link} name={this.linkname} />
            : null}
        </ks-dialog>;
	}
}