ks-newsletter-popup-edrone {
    display: block;
}

ks-newsletter-popup-edrone form {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100%;
    width: 100%;
    max-width: 720px;
}

ks-newsletter-popup-edrone .info {
    padding: 60px 60px 20px 60px;
    fill: #252525;

    flex: 1 0 auto;
    min-height: 10px;
}

ks-newsletter-popup-edrone .info .top {
    width: 100%;
    margin-bottom: 10px;
    font-size: initial;
    max-height: 30px;
}

ks-newsletter-popup-edrone .info .heading {
    width: 100%;
    font-family: var(--font-emphasis);
    font-size: initial;
    max-height: 80px;
    margin-bottom: 10px;
}

ks-newsletter-popup-edrone .info p {
    text-align: center;
    margin-top: 10px;
}

ks-newsletter-popup-edrone .buttons {
    display: flex;
    flex-wrap: wrap;
    min-height: 10px;
    margin-top: 30px;
}

ks-newsletter-popup-edrone .inputs {
    padding: 0px 20px;
}

ks-newsletter-popup-edrone .close {
    color: black !important;
}

ks-newsletter-popup-edrone .button {
    padding: 0px 20px 20px 20px;
}

ks-newsletter-popup-edrone ks-input-check {
    margin: 0px 20px 20px 20px;

    font-size: 13px;
    line-height: 18px;
}

@media (max-width: 720px) {
    ks-newsletter-popup-edrone .info .heading {
        max-height: 50px;
    }

    ks-newsletter-popup-edrone .email {
        font-size: 16px;
    }
}

@media (max-width: 540px) {
    ks-newsletter-popup-edrone .info {
        padding: 70px 25px 20px 25px;
        min-height: 140px;
    }

    ks-newsletter-popup-edrone .info .top {
        display: none;
    }

    ks-newsletter-popup-edrone .info p {
        font-size: 13px;
    }

    ks-newsletter-popup-edrone ks-input-check {
        font-size: 11px;
        line-height: 16px;
    }

    ks-newsletter-popup-edrone ks-input-check {
        margin: 15px;
    }

    ks-newsletter-popup-edrone .info .heading {
        width: 100%;
        max-height: 40px;
    }
}