import { Component, h, Prop,  Host, Build } from '@stencil/core';
import { common } from "../../../global/data/common";
import { loadCommonData } from '../../../global/functions/loadCommon';
import { restoreScrollPosition } from '../../../global/functions/scroll_restore';

@Component({
    tag: 'ks-page-base',
    styleUrl: 'base.css'
})
export class PageBase {
    @Prop() skipbase: boolean;
    @Prop() loadCommon: boolean = true;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;


    componentWillLoad() {
        if(this.loadCommon) loadCommonData(this.commonData, this.commonDynamicData, Build);
    }

    componentDidLoad() {
        restoreScrollPosition(window.location.pathname);
    }

	render() {
		return <Host>
            <ks-cookie-popup/>

            {!this.skipbase && <ks-page-header />}
            <slot />
            {!this.skipbase && <ks-page-footer />}

            <ks-newsletter-popup-edrone displayOnLoad={common.get('newsletterPopup')}/>
            {common.get('newsletterSideButton') ? <ks-newsletter-side-button/> : null}

            {/* <ks-newsletter-popup
                displayOnLoad={common.get('newsletterPopup')}
                api={commonDynamic.get('api').newsletter}
                login-link={common.get('loginLink')}
                register-link={common.get('registerLink')}
                logged-in={commonDynamic.get('loggedIn')}
                email={commonDynamic.get('customer')?.email}
                subscribed={commonDynamic.get('customer')?.subscriber}
            ></ks-newsletter-popup> */}
            <ks-product-suggestions></ks-product-suggestions>
            <ks-error-popup></ks-error-popup>
            <ks-message-popup></ks-message-popup>
            <ks-cookie-popup-details/>
        </Host>;
	}
}




