ks-featured-container {
    display: flex;
    max-width: 1200px;
    width: 100%;
    margin: auto;
    margin-bottom: 20px;
    min-height: 10px;
}

ks-featured-container > .left {
    flex: 1;
    margin-right: 5px;
}

ks-featured-container > .right {
    flex: 1;
    margin-left: 5px;
}

ks-featured-container > div > *:not(:last-child) {
    margin-bottom: 10px;
}

@media (max-width: 840px) {
    ks-featured-container {
        display: block;
    }

    ks-featured-container > .left,
    ks-featured-container > .right {
        margin-right: 0;
        margin-left: 0;
    }

    ks-featured-container > div:first-child > * {
        margin-bottom: 10px;
    }
}