import { Component, State, h } from '@stencil/core';
import { common } from "../../../../global/data/common";
@Component({
    tag: 'ks-navbar-categories',
    styleUrl: 'navbar-categories.css'
})
export class NavbarCategories {

    @State() active: number = -1;
    @State() childActive: number = 0;
    @State() hidden = true;
    @State() hiddenO = true;

    timeout: number;
    delaytimeout: number;

    mouseOver(index: number, deep: boolean) {
        clearTimeout(this.timeout);
        clearTimeout(this.delaytimeout);

        this.delaytimeout = setTimeout(() => {
            
            this.hidden = false;
            this.hiddenO = false;

            this.active = index;
            this.NavbarColor(!deep);
        }, 200);
    }

    mouseLeave() {
        clearTimeout(this.delaytimeout);
        this.delaytimeout = setTimeout(() => {
            this.hiddenO = true;
            this.timeout = setTimeout(() => {
                this.hidden = true;
            }, 200);
            
            this.NavbarColor(true);
        }, 200);
    }

    NavbarColor(state: boolean) {
        const bar = document.querySelector("ks-navbar-categories") as HTMLElement;
        if(!bar) return;

        bar.style.backgroundColor = state ? "var(--navbar-category-color)" : "var(--navbar-color)";
        bar.style.borderTop = state ? "1px solid transparent" : "1px solid var(--navbar-category-color)";
        bar.style.borderBottom = bar.style.borderTop;
    }

    render() {
        
        return <nav aria-label="Kategorie"> {common.get('categories').map( (category, categoryIndex) => {
            const count = category.children.length;
            const childrenstyle = {
                opacity: this.hiddenO ? "0.0" : "1.0",
                height: (count * 40) + "px"
            };

            const hasChildren = count > 0;
            const deep = category.deep;
            const classList = ["category", !deep ? "shallow" : null].join(' ');
    
            return <div class={classList}
                    onMouseOver={()=>this.mouseOver(categoryIndex, deep)}
                    onMouseLeave={()=>this.mouseLeave()}
                    onFocusin={() => this.mouseOver(categoryIndex, deep)}
                    onFocusout={() => this.mouseLeave()}
            >
                <a href={category.url} aria-label={category.name}>
                    {category.name}

                    {hasChildren && <ks-icon name="chevron-down" size={0.8} aria-hidden/>}
                </a>

                {deep ? [
                    <div class="expandedchildren" aria-hidden
                        style={childrenstyle}
                        hidden={this.hidden || this.active != categoryIndex}
                    >
    
                        <div class="buttons">
                            {category.children.map( (child, index) => 
                                <a href={child.url} tabindex={-1}
                                    role="menuitem"
                                    class={'children' in child && index == this.childActive ? "active" : null}
                                    onMouseOver={()=> 'children' in child ? this.childActive = index : null}
                                    onFocusin={()=> 'children' in child ? this.childActive = index : null}
                                >
                                    {child.name}
        
                                    {'children' in child ? null :
                                        <ks-icon name="link" size={0.65}/>
                                    }
                                </a>
                            )}
                        </div>
                                    
                        <div class="content">
                            {category.children.map( (child, index) => 'children' in child ?
        
                                <div hidden={index != this.childActive}>
                                    {child.children.map( sub => 
                                        <a href={sub.url} tabindex={-1} role="menuitem">{sub.name}</a>
                                    )}
                                </div>
                                
                            : null )}
                        </div>
                    </div>

                ]: 
                
                    <div class="simplechildren" style={childrenstyle} hidden={this.hidden || this.active != categoryIndex} aria-hidden>
                        {category.children.map(child => 
                            <a href={child.url} tabindex={-1}>{child.name}</a>    
                        )}
                    </div>
                }
            </div>
        })}</nav>;
    }
}