import { Component, h, Prop, Element } from '@stencil/core';

@Component({
	tag: 'ks-recipe-card',
	styleUrl: 'recipe-card.css'
})
export class RecipeCard {
	@Prop() image: string;
	@Prop() webp: string;
	@Prop() width: number;
	@Prop() height: number;

	@Prop() heading: string;
	@Prop() cuisine: string;
	@Prop() category: string;
	@Prop() link: string;

	@Prop() cuisineLink: string;
	@Prop() categoryLink: string;

	@Prop() hideOnOdd: boolean = false;

	@Prop({reflect: true}) flat: boolean = false;

	@Element() root: HTMLElement;

	render() {


		return<a href={this.link} aria-label={this.heading}>
			<ks-img2 horizontal src={this.image} webp={this.webp} width={this.width} height={this.height} alt={this.heading} aria-hidden/>
			<div class="info">
				<h3>{this.heading}</h3>
				<div>
					<a href={this.cuisineLink}>{this.cuisine}</a>
					<span class="dot">•</span>
					<a href={this.categoryLink}>{this.category}</a>
				</div>
			</div>
		</a>
	}
}
