ks-content-reviews {
    display: block;
    max-width: 1220px !important;
    width: 100%;
    margin: 20px auto 20px auto;
    padding: 0 10px !important;
    box-sizing: border-box;
}

ks-content-reviews > a {
    display: flex;
    border-radius: 20px;
    overflow: hidden;
}

ks-content-reviews .example {
    background-color: white;
    color: black !important;
    margin: 0;
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

    box-sizing: border-box;
}

ks-content-reviews[grey] .example {
    background-color: #f1f1f1;
}

ks-content-reviews .example span {
    color: rgb(255 195 0);
    font-size: 36px;
    font-weight: 500;
    line-height: 16px;
    vertical-align: bottom;
    margin: 0 3px;
}

ks-content-reviews .rating {
    background-color: rgb(255 195 0);
    color: black;
    font-family: var(--font-emphasis);
    font-weight: 700;
    padding: 15px;
    max-width: 210px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

ks-content-reviews .percentage {
    font-size: 56px;
    line-height: 56px;
}

ks-content-reviews .message {
    font-size: 14px;
}

ks-content-reviews .stars {
    display: flex;
    max-width: 150px;
    margin-bottom: 10px;
}

ks-content-reviews .stars .off {
    opacity: 0.5;
}

ks-content-reviews .verification {
    padding: 10px 20px;
    border-top: 1px solid #f2f2f2;
    font-size: 14px;
    color: #595959;
}

ks-content-reviews[grey] .verification {
    border-top: 1px solid #ddd;
    color: #4a4a4a;
}

@media (max-width: 600px) {
    ks-content-reviews > a {
        flex-direction: column;
    }

    ks-content-reviews .rating {
        max-width: 100%;
    }
}