import { Component, h, Prop, Build } from '@stencil/core';
import { home, HomeData } from "../../../global/data/home";
import { loadCommonData } from '../../../global/functions/loadCommon';

@Component({
    tag: 'ks-page-home',
    styleUrl: 'home.css'
})
export class PageHome {
    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;

    @Prop() homeData: string;

    componentWillLoad() {
        const homeDataElement = document.getElementById(this.homeData);
		const homeData = JSON.parse(homeDataElement.innerHTML) as HomeData;

        Object.keys(homeData).map(key => {
			home.set(key as keyof HomeData, homeData[key]);
		});

        loadCommonData(this.commonData, this.commonDynamicData, Build);
    }

	render() {
        const banners = home.get('banners');
        const featured = home.get('featured');
        const info = home.get('info');
        const products = home.get('products');
        const recipes = home.get('recipes');
        const recipesHeading = home.get('recipesHeading');
        const recipesMore = home.get('recipesMore');
        const articles = home.get('articles');
        const articlesHeading = home.get('articlesHeading');
        const articlesMore = home.get('articlesMore');
        const seeMore = home.get('seeMore');
        const review = home.get('reviewAverage');

		return <ks-page-base loadCommon={false} skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>

            {banners ? <ks-banner-container home>
                {banners.map((banner, index) =>
                    <ks-banner sync={index == 0} active={index == 0}
                        name={banner.name} color={banner.color}
                        link={banner.link} image={banner.image} webp={banner.webp}
                        width={banner.width} height={banner.height}
                        theme={banner.theme}
                    />
                )}
            </ks-banner-container> : null}

            {info ? <ks-homepage-info>
                <h1 slot="mainHeading">{info.mainHeading}</h1>
                <p slot="mainContent">{info.mainParagraph}</p>

                <h2 slot="firstHeading">{info.firstHeading}</h2>
                <p slot="firstContent">{info.firstParagraph}</p>

                <h2 slot="secondHeading">{info.secondHeading}</h2>
                <p slot="secondContent">{info.secondParagraph}</p>

                <h2 slot="thirdHeading">{info.thirdHeading}</h2>
                <p slot="thirdContent">{info.thirdParagraph}</p>
            </ks-homepage-info> : null}

            {review ? <ks-content-reviews content={review} /> : null}

            {products ? <ks-homepage-products>
                {products.map(products =>
                    <ks-product-container>
                        {products.products.map(card => 
                            <ks-product-card product={card}/>
                        )}
                    </ks-product-container>
                )}
            </ks-homepage-products> : null}
            

            {featured ? <ks-featured-container>
                {featured.map(banner =>
                    <ks-featured slot={banner.side} name={banner.name} image={banner.image} webp={banner.webp}
                        link={banner.link} width={banner.width} height={banner.height}
                    />
                )}
            </ks-featured-container> : null}

            {recipes ? [
                <h3 aria-label={recipesHeading}>{recipesHeading}</h3>,
                <ks-article-container>
                    {recipes.map(recipe =>
                        <ks-recipe-card heading={recipe.heading} cuisine={recipe.cuisine} category={recipe.category}
                            link={recipe.link} image={recipe.image} webp={recipe.webp}
                            width={recipe.width} height={recipe.height} cuisine-link={recipe.cuisineLink} category-link={recipe.categoryLink} />
                    )}
                </ks-article-container>,
                <ks-see-more href={recipesMore} text={seeMore}></ks-see-more>
            ]: null}

            {articles ? [
                <h3 aria-label={articlesHeading}>{articlesHeading}</h3>,
                <ks-article-container>
                    {articles.map(article =>
                        <ks-article-card link={article.link} description={article.description} heading={article.heading} image={article.image} webp={article.webp} date={article.date} views={article.views} />
                    )}
                </ks-article-container>,
                <ks-see-more href={articlesMore} text={seeMore}></ks-see-more>
            ]: null}
        </ks-page-base>;
	}
}




