import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-featured',
  styleUrl: 'featured.css'
})
export class Featured {

    @Prop() image: string;
    @Prop() webp: string;
    @Prop() name: string;
    @Prop() link: string;

    @Prop() width: number;
    @Prop() height: number;

    @Prop() color: string = "#0f0f0f";

    render() {
        return(
            <a href={this.link} style={{backgroundColor: this.color}}>
                <ks-img2 src={this.image} webp={this.webp} alt={this.name} width={this.width} height={this.height} target="ks-featured"/>
            </a>
        );
    }
}