ks-featured {
    display: block;
    position: relative;
}

ks-featured a {
    max-width: 100%;
    display: block;
    opacity: 1;
    transition: opacity 0.3s ease;
    outline: none;
}

ks-featured a:hover,
ks-featured a:focus {
    display: block;
    opacity: 0.9;
}

ks-featured a:active {
    display: block;
    opacity: 0.8;
}