import { createStore } from "@stencil/store";
import { NavbarTheme, ReviewAverage } from "./common";
import { ProductCardData } from "./listing";
import { RecipeCardData } from "./recipes";
import { ArticleCardData } from "./articles";

export const home = createStore({
    banners: [],
    info: {},
    products: [],
    featured: [],

    recipes: [],
    recipesHeading: "",
    recipesMore: "",

    articles: [],
    articlesHeading: "",
    articlesMore: "",

    seeMore: "",
    logoBanners: []
} as HomeData);

export interface HomeData {
    banners: HomeBannersData[];
    info: HomeInfoData;
    products: HomeProductData[];
    featured: HomeFeaturedData[];
    recipes: RecipeCardData[];
    recipesHeading: string;
    recipesMore: string;

    articles: ArticleCardData[];
    articlesHeading: string;
    articlesMore: string;

    seeMore: string;
    logoBanners: HomeLogoBanner[];
    reviewAverage: ReviewAverage;
}

export interface HomeBannersData {
    name: string;
    link: string;
    color: string;
    image: string;
    webp: string;
    width: number;
    height: number;
    theme: NavbarTheme;
}

export interface HomeInfoData {
    mainHeading: string;
    mainParagraph: string;
    firstHeading: string;
    firstParagraph: string;
    secondHeading: string;
    secondParagraph: string;
    thirdHeading: string;
    thirdParagraph: string;
}

export interface HomeProductData {
    name: string;
    products: ProductCardData[];
}

export interface HomeFeaturedData {
    name: string;
    link: string;
    image: string;
    webp: string;
    width: number;
    height: number;
    side: string;
}

export interface HomeLogoBanner {
    name: string;
    image: string;
    webp?: string;
    link: string;
    width: number;
    height: number;
}