import { Component, h, Prop } from '@stencil/core';
import { ReviewAverage } from '../../../global/data/common';

@Component({
	tag: 'ks-content-reviews',
    styleUrl: 'reviews.css'
})
export class ContentReviews {
    @Prop({reflect: true}) grey: boolean;
    @Prop() content: ReviewAverage;

	render() {
        const percentage = ((this.content.average / 5) * 100).toFixed(0);

        const message = this.content.message
            .replace("{reviews}", this.content.reviews.toString())
            .replace("{months}", this.content.months.toString())
            .replace("{totalReviews}", this.content.totalReviews.toString());

        const ariaLabel = `Opinie klientów - ${percentage}% pozytywnych opinii weryfikowanych pod kątem dokonania zakupu i otrzymania produktu. Wybrana opinia: "${this.content.example}".`

		return <a href={this.content.href} target="_blank" aria-label={ariaLabel}>
            <div class="rating">
                <div class="percentage">{percentage}%</div>
                <div class="stars" aria-hidden>
                    {this.star(Math.round(this.content.average) >= 1)}
                    {this.star(Math.round(this.content.average) >= 2)}
                    {this.star(Math.round(this.content.average) >= 3)}
                    {this.star(Math.round(this.content.average) >= 4)}
                    {this.star(Math.round(this.content.average) >= 5)}
                </div>
                <div class="message">{message}</div>
            </div>
            <div class="example">
                <blockquote>
                    <span aria-hidden>&ldquo;</span>
                    {this.content.example}
                    <span aria-hidden>&rdquo;</span>
                </blockquote>
                <div class="verification">
                    Opinie są weryfikowane pod kątem dokonania zakupu i otrzymania produktu.
                </div>
            </div>
        </a>;
	}

    star(on) {
        return <svg class={on ? "" : "off"} width="100%" height="100%" viewBox="0 0 50 50" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlSpace="preserve" style={{fillRule: "evenodd", clipRule: "evenodd", strokeLinejoin: "round", strokeMiterlimit: "2"}}>
            <g transform="matrix(1,0,0,1,0,2.38729)">
                <path d="M25,0L30.613,17.275L48.776,17.275L34.082,27.951L39.695,45.225L25,34.549L10.305,45.225L15.918,27.951L1.224,17.275L19.387,17.275L25,0Z" style={{fill:"black"}}/>
            </g>
        </svg>;
    }
}
