import { Component, h, Prop, Method, Element, State, Listen } from '@stencil/core';

@Component({
	tag: 'ks-sidepanel',
	styleUrl: 'sidepanel.css'
})
export class SidePanel {
    @Element() root: HTMLElement;
    overlay: HTMLKsOverlayElement;

    @Prop({reflect: true}) left: boolean;
    @Prop({reflect: true}) large: boolean;

    @State() name: string = "";
    @State() message: string = "";
    @State() stack: string = "";

    componentDidRender() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    @State() visible: string = "";

    @Method() async show() {
        this.overlay.show();
        setTimeout(() => {
            this.visible = "visible";
        }, 10);
    }

    @Method() async hide() {
        this.visible = "hidden";
        this.overlay.hide();
    }

    @Listen("closed") onClosed() {
        this.visible = "hidden";
    }

	render() {
		return <ks-overlay dark>
            <div class={`content ${this.visible}`}>
                <button class="close" onClick={()=>this.hide()} aria-label="Zamknij panel">
                    <ks-icon name="x" aria-hidden></ks-icon>
                </button>

                <slot/>
            </div>
        </ks-overlay>;
	}
}