import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-navbar-button',
    styleUrl: 'navbar-button.css'
})
export class NavbarButton {

    @Prop() link: string;
    @Prop() icon: string;
    @Prop() count: number;
    @Prop() name: string;
    @Prop({reflect: true}) last: boolean = false;

    render() {
        const ariaLabel = this.count && this.count > 0 
            ? `${this.name}, elementy: ${this.count}`
            : this.name;

        const content = [
            <ks-icon name={this.icon} size={1.4} aria-hidden="true" />,
            this.count && this.count != 0 ? 
                <span class="count" aria-hidden="true">
                    {this.count}
                </span>
            : null
        ]

        return [
            this.link ? <a class="button" href={this.link} role="button" aria-label={ariaLabel}>
                {content}
            </a> : <button class="button" aria-label={ariaLabel}>
                {content}
            </button>,
            <div aria-hidden="true">
                {this.name}
            </div>
        ];
    }
}