import { Component, h, Prop, Element, Host } from '@stencil/core';
import { window_load } from '../deferredpromise';

import Swiper from 'swiper';
import {Pagination, Autoplay, A11y} from 'swiper/modules';

Swiper.use([Pagination, Autoplay, A11y]);


@Component({
  tag: 'ks-banner-container',
  styleUrl: 'banner-container.css'
})
export class BannerContainer {
    @Element() root: HTMLElement;
    @Prop() delay: number = 0;
    @Prop() autoplay: number = 5000;

    @Prop({reflect: true}) home: boolean;

    carousel: Swiper;

    window: boolean = false;
    rendered: boolean = false;
    @Prop({reflect: true, mutable: true}) loaded: boolean = false;

    componentDidLoad() {
        window_load.promise.then(()=>this.initialize());
    }

    initialize() {
        setTimeout(() => {
            this.carousel = new Swiper('ks-banner-container .swiper-container', {
                observer: true,
                observeParents: true,

                centeredSlides: true,
                slidesPerView: "auto",
                preventInteractionOnTransition: true,

                grabCursor: true,
                autoplay: {
                    delay: this.autoplay,
                },
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                }
            });

            this.carousel.on('slideChange', () => {
                this.root.querySelectorAll('ks-banner').forEach((element, index) => {
                    if(index == this.carousel.activeIndex)
                        element.setAttribute('active', 'active');
                    else element.removeAttribute('active');
                });
            })

            this.loaded = true;
        }, this.delay);
    }

    render() {
        return <Host>
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <slot />
                </div>
            </div>
            <div class="pagination">
                {this.home ? <svg height="36" width="36"><polygon points="0,0 36,0 36,36"></polygon></svg> : null}
                <div class="swiper-pagination">
                </div>
                {this.home ? <svg height="36" width="36"><polygon points="0,0 36,0 0,36"></polygon></svg> : null}
            </div>
        </Host>;
    }
}