ks-button {
    display: block;
    position: relative;
}

ks-button > * {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    
    font-size: var(--product-button-size);
    width: 100%;
    padding: 12px 20px;

    outline-offset: 4px;
    border-style: none;
    text-decoration: none !important;

    cursor: pointer;
    border-radius: 0px;

    font-family: var(--font-regular);
    font-size: 15px;

    border: 1px solid var(--color-dark);
    background-color: var(--color-dark);
    color: var(--text-color-dark) !important;

    
    transition: var(--transition-background-color),
                var(--transition-border-color);
}

ks-button ks-img3 {
    max-height: 50px;
}

ks-button:not([transitionless]) > *:hover { background-color: var(--color-dark-hover); }
ks-button:not([transitionless]) > *:active { background-color: var(--color-dark-active); }

ks-button[rounded] > * {
    border-radius: 10px;
}

ks-button[round] > * {
    border-radius: 100px;
}

ks-button[narrow] > * {
    padding: 9px 20px;
}

ks-button[narrower] > * {
    padding: 6px 18px;
}

ks-button[tall] > * {
    padding: 18px 20px;
}

ks-button:not([name]) > * {
    padding: 12px;
}

ks-button:not([name])[narrow] > * {
    padding: 9px;
}

ks-button:not([name])[narrower] > * {
    padding: 6px;
}

ks-button:not([name])[tall] > * {
    padding: 18px;
}

ks-button[primary] > * {
    color: var(--text-color-primary) !important;
    background-color: var(--color-primary) !important;
    border: 1px solid var(--color-primary) !important;
}
ks-button[primary]:not([transitionless]) > *:hover { background-color: var(--color-primary-hover); }
ks-button[primary]:not([transitionless]) > *:active { background-color: var(--color-primary-active); }


ks-button[secondary] > * {
    color: var(--text-color-secondary) !important;
    background-color: var(--color-secondary) !important;
    border: 1px solid var(--color-secondary) !important;
}
ks-button[secondary]:not([transitionless]) > *:hover { background-color: var(--color-secondary-hover); }
ks-button[secondary]:not([transitionless]) > *:active { background-color: var(--color-secondary-active); }


ks-button[border] > * { 
    border: 1px solid var(--color-dark);
    color: var(--color-dark) !important;
    background-color: rgba(0, 0, 0, 0);
}

ks-button[border]:not([transitionless]) > *:hover { background-color: rgba(0, 0, 0, 0.096); }
ks-button[border]:not([transitionless]) > *:active { background-color: rgba(0, 0, 0, 0.336); }


ks-button[border][light] > * {
    border: 1px solid #ffffff;
    color: var(--text-color-dark) !important;
    background-color: rgba(255, 255, 255, 0);
}
ks-button[border][light]:not([transitionless]) > *:hover { background-color: rgba(255, 255, 255, 0.096); }
ks-button[border][light]:not([transitionless]) > *:active { background-color: rgba(255, 255, 255, 0.336); }

ks-button[disabled] > * {
    background-color: #e2e2e2 !important;
    border-color: #bbbbbb;
    color: #959595 !important;
}

ks-button[border][muted] > * {
    border: 1px solid #e5e5e5;
}

ks-button[border][muted]:not([transitionless]) > *:hover {
    border-color: #bebebe;
    background-color: rgba(255, 255, 255, 0);
}

ks-button[border][muted]:not([transitionless]) > *:active {
    border-color: #8d8d8d;
    background-color: rgba(255, 255, 255, 0);
}

ks-button[name] ks-icon {
    margin-left: 10px;
}

ks-button[left][name] ks-icon {
    margin-right: 10px;
    margin-left: 0;
}

ks-button[transparent] > * { 
    border: none;
    outline-offset: 0px;
    color: var(--color-dark) !important;
    background-color: rgba(0, 0, 0, 0);
}

ks-button[transparent]:not([transitionless]) > *:hover { background-color: rgba(0, 0, 0, 0.096); }
ks-button[transparent]:not([transitionless]) > *:active { background-color: rgba(0, 0, 0, 0.336); }

ks-button[icon][name] ks-icon {
    margin: 0 4px;
}

ks-button[light]:not([border]) > *       {
    color: black !important;
    background-color: rgba(255, 255, 255, 1);
    border: 1px solid #ffffff;
}
ks-button[light]:not([border]) > *:hover  { background-color: rgba(255, 255, 255, 0.692); }
ks-button[light]:not([border]) > *:active { background-color: rgba(255, 255, 255, 0.452); }


ks-button[loading] > * {
    overflow: hidden;
    color: transparent !important;
    cursor: progress;
}