import { Component, h, Prop, Host } from '@stencil/core';
import { NavbarTheme } from '../../global/data/common';
@Component({
	tag: 'ks-banner',
	styleUrl: 'banner.css'
})
export class Banner {

	@Prop() name: string;
	@Prop() color: string;
	@Prop() image: string;
	@Prop() webp: string;
	@Prop() link: string;
	@Prop() sync: boolean;

	@Prop() width: number;
	@Prop() height: number;

	@Prop({reflect: true, mutable: true}) active: boolean;
	
	@Prop() theme: NavbarTheme;

	Newsletter(name) {
		if(name == "Newsletter")
			document.querySelector('ks-newsletter-popup')?.Show();
	}

	render() {
		const theme = (this.active && this.theme) ? `:root {
			--navbar-color: ${this.theme.navbarColor} !important;
			--navbar-color-hover: ${this.theme.navbarColorHover} !important;
			--navbar-color-active: ${this.theme.navbarColorActive} !important;
			--navbar-category-color: ${this.theme.categoryColor} !important;
			--navbar-category-hover: ${this.theme.categoryColorHover} !important;
			--navbar-category-active: ${this.theme.categoryColorActive} !important;
			--navbar-category-backdrop: ${this.theme.categoryColorBackdrop} !important;
		}` : null;

		return <Host class="swiper-slide" style={{backgroundColor: this.color}}>
			<a href={this.link ? this.link : null} onClick={()=>this.Newsletter(this.name)}>
				<ks-img2 vertical
						sync={this.sync}
						src={this.image}
						webp={this.webp}
						alt={this.name}
						width={this.width}
						height={this.height}
                ></ks-img2>
			</a>

			{theme ? <style innerHTML={theme}></style> : null}
		</Host>;
	}
}