ks-navbar-language-panel nav {
    padding: 10px 0px 10px 0px !important;
}

ks-navbar-language-panel .language,
ks-navbar-language-panel .currency {
    display: flex;
    padding: 10px 23px;
    height: 50px;
    max-height: 50px;
    justify-content: space-between;
    align-items: center;
}

ks-navbar-language-panel .language.active,
ks-navbar-language-panel .currency.active {
    background-color: #e1e1e1;
}

ks-navbar-language-panel .language:hover,
ks-navbar-language-panel .currency:hover {
    background-color: #ececec;
}

ks-navbar-language-panel .language:active,
ks-navbar-language-panel .currency:active {
    background-color: #cecece;
}


ks-navbar-language-panel .flag {
    max-height: 35px;
    max-width: 35px;
    border-radius: 50px;
    border: 2px solid #303030;
    background-color: #303030;
    overflow: hidden;
}


ks-navbar-language-panel .tag {
    background-color: black;
    color: white;
    font-size: 14px;
    display: flex;
}

ks-navbar-language-panel .code {
    padding: 5px 10px;
    width: 27px;
    text-align: center;
}

ks-navbar-language-panel .symbol {
    background-color: #303030;
    padding: 5px 10px;
    width: 15px;
    text-align: center;
}

ks-navbar-language-panel .title {
    padding: 10px 23px;
    border-bottom: 1px solid black;

    font-size: 18px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

ks-navbar-language-panel .title:not(:first-of-type) {
    margin-top: 20px;
}