ks-development-warning {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 15px;

    font-family: var(--font-emphasis);
    font-size: 17px;
    font-weight: 700;
    line-height: 15px;
    user-select: none;

    background-color: rgb(255, 196, 0);
    color: rgb(0, 0, 0);
}

ks-development-warning > span {
    margin-right: 8px;
}


@media only screen and (max-width: 959px) {
    ks-development-warning {
        font-size: 14px;
        line-height: 14px;
    }
}

@media only screen and (max-width: 480px) {
    ks-development-warning {
        font-size: 13px;
        line-height: 13px;
    }
}