ks-navbar-button {
    display: block;
    position: relative;
    height: 70px;
}

ks-navbar-button > .button {
    text-decoration: none !important;
    line-height: 70px;
    min-width: 60px;
    background-color: transparent;
    border: none;
    z-index: 1;
    position: relative;
    outline: none;

    color: var(--navbar-text-color) !important;

    transition: var(--transition-background-color);
}

ks-navbar-button > .button:hover,
ks-navbar-button > .button:focus {
    background-color: var(--navbar-color-hover);
}

ks-navbar-button > .button:active {
    background-color: var(--navbar-color-active);
}

ks-navbar-button > div {
    position: absolute;
    bottom: -28px;
    z-index: 2147483649;

    width: 250%;
    left: -75%;

    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
    color: var(--navbar-text-color);

    opacity: 0;
    transition: opacity 0.3s ease;
    user-select: none;
    pointer-events: none;
}

ks-navbar-button > .button:hover + div {
    opacity: 1;
}

ks-navbar-contact > div ks-navbar-button > div {
    display: none;
}

@media only screen and (max-width: 1000px) {
    ks-navbar-button > div {
        display: none;
    }
}

ks-navbar-button .count {
    position: absolute;
    top: 0;
    right: 0;
    
    min-width: 30px;
    height: 30px;

    padding: 0px 0px;
    margin-top: 5px;

    border-radius: 500px;
    box-sizing: border-box;
    vertical-align: middle;

    background: var(--color-secondary);
    color: var(--navbar-text-color);
    font-size: 16px;

    display: inline-flex;
    justify-content: center;
    align-items: center;
}

ks-navbar-button > .button {
    display: flex !important;
    align-items: center;
    justify-content: center;
    height: 100%;
}

ks-navbar-button ks-icon svg.medium {
    stroke-width: 1.2;
}


ks-navbar-button[last] > div {
    width: 200px;
    right: 0px;
    left: unset;
    text-align: right;
}