import { Component, h, Element, Host, Build } from '@stencil/core';
import { commonDynamic } from '../../global/data/commonDynamic';
import { marked } from 'marked';

@Component({
  tag: 'ks-cookie-popup-details',
  styleUrl: 'cookie-popup-details.css'
})
export class CookiePopup{
    @Element() root: HTMLElement;

    render() {
        if (!Build.isBrowser)
            return <Host/>;

        const consent = commonDynamic.get('consent');
        const details = marked(consent.details ?? "")

        return <Host>
            <ks-dialog id="cookie-popup-details" dark>
                <div class="details" innerHTML={details} />
            </ks-dialog>
        </Host>;
    }

}
