import { Component, h, Prop, Element, Method } from '@stencil/core';
import ValidateInput from '../../input/validate';
declare var ksNewsletterToken: any;

import { SubscriptionData, commonDynamic } from "../../../global/data/commonDynamic";
import { DataLayer } from '../../../global/dataLayer';
import { common } from '../../../global/data/common';
import { formfetch } from '../../../global/functions/fetch';

@Component({
  tag: 'ks-newsletter-popup-edrone',
  styleUrl: 'newsletter-popup-edrone.css'
})
export class NewsletterPopupEdrone {
    @Element() root: HTMLElement;
    dialog: HTMLKsDialogElement;

    @Prop() displayOnLoad: boolean = false;
    success = false;

    componentDidLoad() {
        this.dialog = this.root.querySelector("ks-dialog");
        if(this.displayOnLoad && document.cookie.search("newsletterPopup=tak") == -1) {
            setTimeout(() => {
                this.Show();
            }, 4000);
        }
    }

    async requestHandler(event: Event) {
        event.preventDefault();
        if(!await ValidateInput(this.root.querySelector('form')))
            return;

        this.dialog.showLoading();

        //document.querySelector('ks-cookie-popup').requireconsent('newsletter').then(consentGranted => {
            const strings = common.get('translations');

            // if(!consentGranted) {
            //     this.dialog.showFailure(strings.newsletterPopupFailureHeading, strings.newsletterPopupFailureMessage);
            //     return;
            // }

            const target = event.target as HTMLFormElement;
            const data = new FormData(target);
            
            const email = data.get('email');
            const name = data.get('name');
            const zgoda = data.get('zgoda');
            const newsletterAPI = commonDynamic.get('api').newsletter;

            // Server side call
            formfetch(newsletterAPI, {"email": email.toString(), "name": name.toString(), "zgoda": zgoda.toString()});

            this.track(data.get('email'), data.get('name'));
            
            this.dialog.showSuccess(strings.newsletterPopupSuccessHeading, strings.newsletterPopupSuccessMessage);
            this.success = true;
        //});
    }

    track(email, name) {
        const data: SubscriptionData = {
            email: email,
            name: name,
            subscriber: true,
            place: "Zapis do newslettera | Popup"
        }

        DataLayer.subscribe(data);
    }
    

    @Method()
    async Show() {
        this.dialog.show();
    }

    SetCookie() {
        var expiration = "";
        var expirationDate = new Date();
        expirationDate.setMonth(expirationDate.getMonth() + 1);
        expiration = "expires=" + expirationDate.toUTCString() + "; ";

        document.cookie = "newsletterPopup=tak; " + expiration + "path=/";

        if(this.success) {
            this.success = false;
        }
    }
    

  render() {
    const strings = common.get('translations');

    return [
        <ks-dialog dark smallmobile nopadding onClosed={()=>this.SetCookie()}>
            <form onSubmit={e => this.requestHandler(e)}>
                <div class="info">
                    <svg class="top" viewBox={strings.newsletterPopupHeadingTopSize} width={909} height={45}>
                        <text x="0" y="12">{strings.newsletterPopupHeadingTop}</text>
                    </svg>

                    <svg class="heading" viewBox={strings.newsletterPopupHeadingBottomSize} width={960} height={130}>
                        <text x="-1" y="12">{strings.newsletterPopupHeadingBottom}</text>
                    </svg>

                    <p>{strings.newsletterPopupMessage}</p>
                </div>
                
                <div class="inputs">
                    <ks-input-text email center required placeholder={strings.newsletterPopupEmail} name="email"/>
                    <ks-input-text center required placeholder={strings.newsletterPopupName} name="name" />
                </div>
                <ks-input-check large required nomessage name="zgoda" label={strings.newsletterPopupAgreement} />
                <div class="button">
                    <ks-button submit round name={strings.newsletterPopupSubmit}/>
                </div>
            </form>
        </ks-dialog>
    ];
  }
}
