import { Component, h, Prop } from '@stencil/core';

@Component({
	tag: 'ks-article-card',
	styleUrl: 'articleCard.css'
})
export class ArticleCard {
	@Prop() image: string;
	@Prop() webp: string;
	@Prop() heading: string;
	@Prop() description: string;
	@Prop() date: string;
	@Prop() views: number;
	@Prop() link: string;

	@Prop() width: number;
	@Prop() height: number;

	@Prop({reflect: true}) hideOnOdd: boolean = false;
	@Prop({reflect: true}) flat: boolean = false;

	render() {
		const ariaLabel = `${this.heading} - ${this.description}`

		return <a href={this.link} aria-label={ariaLabel}> 
			<div class="image">
				<ks-img2 horizontal center src={this.image} webp={this.webp} alt={this.heading} width={600} height={300} />
			</div>

			<div class="info">
				<h3>{this.heading}</h3>
				<div class="meta">
					<ks-icon name="calendar" size={0.9} aria-hidden/> {this.date}
				</div>
				
				<p>{this.description}</p>
			</div>
		</a>
	}
}
