import { Component, h, Host, Prop } from '@stencil/core';

@Component({
    tag: 'ks-loader',
    styleUrl: "loader.css"
})
export class Loader {
    @Prop({reflect: true}) running: boolean = true;
    @Prop({reflect: true}) large: boolean;
    @Prop({reflect: true}) oversized: boolean;
    @Prop({reflect: true}) dark: boolean;

	render() {
        if(!this.running) return;
        return <Host role="status" aria-live="polite" aria-label="Ładowanie treści" />;
	}
}
