import { Component, h, Prop } from '@stencil/core';
import { common} from '../../../global/data/common';

@Component({
    tag: 'ks-navbar-language-button',
    styleUrl: 'navbar-language-button.css'
})
export class NavbarLanguageButton {

    @Prop() link: string;
    @Prop() icon: string;
    @Prop() count: number;
    @Prop() name: string;

    @Prop({reflect: true}) sidebar: boolean;

    render() {
        const language = common.get("language")
        const flag = language.flag;
        
        const currency = common.get("currency")
        const symbol = currency.symbol;
        const small = currency.symbol.length > 1 ? " small" : "";

        return [
            <div class="button" aria-label={this.name}>
                <div class="flag">
                    <ks-img2 src={flag}/>
                </div>
                {this.sidebar ? null : <span class={"symbol" + small}>{symbol}</span>}
            </div>,
            <div class="name">
                {this.name}
            </div>
        ];
    }
}