ks-navbar-categories nav {
    display: flex;

    background-color: var(--navbar-category-color);
    transition: background-color 0.2s ease;
    height: 32px;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    position: relative;
}


@media only screen and (max-width: 959px) {
    ks-navbar-categories nav {
        display: none;
    }
}

ks-navbar-categories nav .category {
    display: inline-flex;
    align-items: baseline;
    height: 32px;
    outline: 1px solid transparent;
    color: white;
}

ks-navbar-categories nav .category.shallow {
    position: relative;
}

ks-navbar-categories nav .category > a {
    padding: 0 8px 0 10px;
    line-height: 36px;
    text-decoration: none;
    outline: none;

    transition: var(--transition-color), var(--transition-background-color);
    color: var(--navbar-category-text-color);

    font-size: .875rem;
}

ks-navbar-categories nav .category:first-child > a {
    padding: 0 8px 0 15px;
}

ks-navbar-categories nav .category > a:hover,
ks-navbar-categories nav .category > a:focus {
    text-decoration: none;
    background-color: white;
    color: black;
}

ks-navbar-categories nav .category > div {
    background-color: white;
    color: #252525;
    position: absolute;
    z-index: 10000;
    top: 33px;
    left: 0px;
    min-width: 200px;

    opacity: 0.0;
    transition: opacity 0.2s ease;

    box-shadow: 0 10px 15px -10px rgba(0, 0, 0, 0.26)
}



ks-navbar-categories nav .simplechildren a {
    display: block;
    padding: 9px 15px;
    text-decoration: none;

    font-size: 14px;
    text-align: left;

    box-sizing: border-box;
    border-bottom: 1px solid #f2f2f2;

    transition: background-color 0.3s ease;
    color: #252525;
}

ks-navbar-categories nav .simplechildren a:hover {
    text-decoration: none;
    color: #252525;

    background-color: #f9f9f9;
}

ks-navbar-categories nav .simplechildren a:active {
    text-decoration: none;
    background-color: #e6e6e6;
}

ks-navbar-categories nav .expandedchildren .buttons {
    background-color: var(--navbar-category-backdrop);
    transition: background-color 0.2s ease;
    width: 230px;
}

ks-navbar-categories nav .expandedchildren .buttons a {
    display: block;
    padding: 10px 15px;
    width: 100%;
    height: 40px;
    
    background-color: var(--navbar-category-color);
    transition: background-color 0.2s ease;
    color: white;
    border: none;
    cursor: pointer;
    text-decoration: none;

    box-sizing: border-box;
    border-bottom: 1px solid transparent;

    transition: background-color 0.3s ease, border-right-color 0.3 ease;

    border-right: 1px solid transparent;

    font-family: inherit;
    font-size: 14px;
    text-align: left;
}

ks-navbar-categories nav .expandedchildren .buttons a ks-icon {
    float: right;
    margin-top: 2px;
}

ks-navbar-categories nav .expandedchildren .buttons a:hover {
    background-color: var(--navbar-category-hover);
    color: var(--navbar-category-text-color);
    text-decoration: none;
}

ks-navbar-categories nav .expandedchildren .buttons a:active {
    background-color: var(--navbar-category-active);
    color: var(--navbar-category-text-color);
    text-decoration: none;
}

ks-navbar-categories nav .expandedchildren .buttons a.active {
    background-color: white;
    color: #252525;
    border-right-color: #f2f2f2;
}

ks-navbar-categories nav .expandedchildren .content {
    flex: 1 1 auto;
    min-width: 1px;
}

ks-navbar-categories nav .expandedchildren .content > div {
    display: flex;
    height: 100%;

    flex-flow: wrap column;
    align-content: flex-start;
    align-items: flex-start;
}

ks-navbar-categories nav .expandedchildren .content > div[hidden] {
    display: none;
}

ks-navbar-categories nav .expandedchildren .content a {
    display: block;
    width: 230px;
    height: 40px;

    padding: 9px 15px;
    text-decoration: none;

    font-size: 14px;
    text-align: left;

    box-sizing: border-box;
    border-bottom: 1px solid #f6f6f6;
    border-right: 1px solid #f2f2f2;

    transition: background-color 0.3s ease;
    color: #252525;
}

ks-navbar-categories nav .expandedchildren .content a:hover {
    text-decoration: none;
    color: #252525;

    background-color: #f9f9f9;
}

ks-navbar-categories nav .expandedchildren {
    width: 100%;
    min-height: 240px;
    display: flex;

    visibility: visible;
}

ks-navbar-categories nav .expandedchildren[hidden] {
    visibility: hidden;
}