ks-navbar {
    display: block;
    min-height: 104px;
    background-color: var(--navbar-color);
    transition: background-color 0.2s ease;
}

ks-navbar > nav {
    display: flex;

    position: relative;

    color: var(--navbar-text-color);
}

ks-navbar > nav > .logo {
    display: flex;
    flex: 1;
    align-items: center;
}

ks-navbar > nav > .search {
    display: flex;
    flex: unset;
    justify-content: center;
    align-items: center;
}

ks-navbar > nav > .buttons {
    display: flex;
    flex: unset;
    justify-content: flex-end;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
}

ks-navbar > nav > .buttons.loaded {
    opacity: 1;
}

ks-navbar > nav > .logo > div {
    margin-right: auto;
    margin-left: 15px;
    max-width: 217px;
    width: 100%;

    justify-content: flex-start;
}

ks-navbar > nav > .logo > div ks-img {
    max-width: 217px;
    margin-right: 15px;
}

ks-navbar > nav > .logo > div .promo {
    display: block;
    font-size: 11px;
    font-weight: 700;
    text-decoration: none;
    color: var(--navbar-text-color);
    white-space: nowrap;
}


@media only screen and (min-width: 640px) {
    ks-navbar > nav > .logo { flex: 1; }
    ks-navbar > nav > .buttons { flex: 1; }
}

@media only screen and (min-width: 1400px) {
    ks-navbar > nav > .search { flex: 1; }

    ks-navbar > nav > .logo > div ks-img {
        width: 217px;
    }

    ks-navbar > nav > .logo > div {
        display: flex;
        align-items: center;

        max-width: unset;
        width: unset;
    }

    ks-navbar > nav > .logo > div .promo {
        margin-left: 15px;
        display: block;
        padding: 8px 20px 5px 20px;
        border-radius: 50px;

        background-color: var(--navbar-text-color);
        color: var(--navbar-color);

        font-size: 13px;
        font-weight: 700;
    }

    ks-navbar > nav > .logo > div a.promo {
        box-shadow: 0 0 0px rgba(255, 255, 255, 0.529);
        transition: box-shadow 0.2s ease;
    }

    ks-navbar > nav > .logo > div a.promo:hover {
        box-shadow: 0 0 15px rgba(255, 255, 255, 0.529);
    }

    ks-navbar > nav > .logo > div a.promo:active {
        box-shadow: 0 0 8px rgba(255, 255, 255, 0.529);
    }
}

#ks-navbar-menu-buttons {
    box-sizing: border-box;
    min-height: 70px;
    padding: 0 15px;
    text-decoration: none;
}

@media only screen and (max-width: 639px) {
    ks-navbar #navbar-button-fav,
    ks-navbar #navbar-button-contact {
        display: none;
    }
}

@media only screen and (max-width: 959px) {
    ks-navbar #navbar-button-logout,
    ks-navbar #navbar-button-login,
    ks-navbar #navbar-button-account,
    ks-navbar .buttons > ks-navbar-language-button {
        display: none;
    }
}

@media only screen and (min-width: 960px) {
    ks-navbar #navbar-button-menu {
        display: none;
    }
}