ks-newsletter-side-button {
    position: fixed;
    right: 0;
    top: 200px;
    z-index: 10000;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    display: flex;
    
    color: white;
    font-size: 18px;
    font-family: var(--font-emphasis);
    
    padding: 20px 8px;
    border-radius: 20px 0px 0px 20px;
    user-select: none;

    background-color: #000000;
    transition: background-color 0.2s ease;
}

@media (max-width: 1260px) or (max-height: 540px) {
    ks-newsletter-side-button {
        display: none;
    }
}

ks-newsletter-side-button:hover {
    background-color: #1f1f1f;
}

ks-newsletter-side-button:active {
    background-color: #2c2c2c;
}

ks-newsletter-side-button .label {
    font-size: 20px;
    line-height: 20px;
}

ks-newsletter-side-button ks-icon {
    transform: rotateZ(45deg);
    margin: 15px 0px;
}

ks-newsletter-side-button .benefit {
    text-transform: uppercase;
    font-weight: 700;
}