import { Component, h, Prop, State, Watch, Element, Event, EventEmitter } from '@stencil/core';
import { window_load } from '../deferredpromise';
@Component({
    tag: 'ks-img2',
    styleUrl: "img2.css"
})
export class Img2 {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) src: string;
    @Prop() webp: string;
    @Prop() alt: string;
    
    @Prop() target: string;
    @Prop() observerMargin: string;
    @Prop() sync: boolean = false;

    @Prop() width: number;
    @Prop() height: number;

    @Prop({reflect: true}) vertical: boolean;
    @Prop({reflect: true}) horizontal: boolean;
    @Prop({reflect: true}) limit: boolean;
    @Prop({reflect: true}) fill: boolean;

    @Prop({reflect: true}) center: boolean;

    @State() loaded: boolean = false;

    @Event() lazyLoaded: EventEmitter;
    loadHandler(e: Event) {
        if (e.target instanceof HTMLElement && !e.target.getAttribute("src")?.includes('data:image')) {
            this.loaded = true;
            this.lazyLoaded.emit();
        }
    }
    
    @Watch('src') srcListener() {
        const image = this.root.querySelector("img");
        image.removeAttribute("src");
        image.setAttribute("src", this.src);

        setTimeout(()=>{
            if(!image.complete) {
                this.loaded = false;
            }
        }, 200);
    }

    private observer: IntersectionObserver;
    private image: HTMLImageElement;
    private source: HTMLSourceElement;

    componentDidLoad() {
        if(!this.sync)
            window_load.promise.then(()=>this.initializeObserver());
    }

    initializeObserver() {
        this.image = this.root.querySelector('img') as HTMLImageElement;
        this.source = this.root.querySelector('source') as HTMLSourceElement;
        const target = this.target ? this.root.closest(this.target) : this.root.parentElement;
        
        const onIntersection = (entries)=> {
            for (const entry of entries) {
                if (entry.isIntersecting) {
                    if (this.observer) {
                        this.observer.disconnect();
                    }
                    if (this.source?.getAttribute('data-srcSet')) {
                        this.source.setAttribute('srcSet', this.source.getAttribute('data-srcSet'));
                        this.source.removeAttribute('data-srcSet');
                    }
                    if (this.image.getAttribute('data-src')) {
                        this.image.setAttribute('src', this.image.getAttribute('data-src'));
                        this.image.removeAttribute('data-src');
                    }
                }
            }
        }

        if (this.image && target) {
            this.observer = new IntersectionObserver(onIntersection, {
                rootMargin: this.observerMargin
            });
            this.observer.observe(target);
        }
    }

	render() {
        const loading = this.loaded ? "" : "loading";

        if(this.sync) return [
            <picture>
                {this.webp ?
                    <source srcSet={this.webp} type="image/webp"/>
                : null}

                <img src={this.src} alt={this.alt} width={this.width} height={this.height} />
                
                
            </picture>,
            this.fill ? <canvas width={this.width} height={this.height}></canvas> : null
        ];

        return [
            <picture>
                {this.webp ?
                    <source data-srcSet={this.webp} type="image/webp"/>
                : null}

                <img class={loading} onLoad={(e)=>this.loadHandler(e)} data-src={this.src}
                    alt={this.alt} width={this.width} height={this.height} />
            </picture>,

            (!this.loaded ? <ks-loader dark /> : null),
            !this.loaded || this.fill ? <canvas width={this.width} height={this.height}></canvas> : null,
        ];
	}
}
