ks-page-footer {
    display: block;
    overflow: hidden;

    background-color: var(--footer-color);
    color: var(--footer-text-color);
    font-size: 16px;
}

ks-page-footer .about {
    display: flex;
    justify-content: space-between;

    padding: 50px 70px 40px 70px;
    margin: auto;
}

ks-page-footer .contact > p:first-child {
    display: block;
    color: var(--footer-heading-color);
    font-family: var(--font-emphasis);
    font-weight: 700;
    font-size: 18px;
    margin: 0 0 15px 0;
}

ks-page-footer .contact a,
ks-page-footer .contact > p {
    display: block;
    text-decoration: none !important;
    font-size: 16px;
    margin: 0 0 5px 0;

    color: var(--footer-text-color);
    transition: var(--transition-color);
}

ks-page-footer .contact a:hover  { color: var(--footer-text-color-hover); }
ks-page-footer .contact a:active { color: var(--footer-text-color-active); }

ks-page-footer .contact ks-icon {
    margin-right: 5px;
}

ks-page-footer .links {
    display: flex;
    justify-content: flex-start;
    min-height: 100px;
}

ks-page-footer .newsletter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column nowrap;
    text-align: center;

    padding: 0 0 0 30px;
    flex-shrink: 0;
}

ks-page-footer .newsletter > div {
    font-family: var(--font-emphasis);
}

ks-page-footer .newsletter > div:last-of-type {
    font-size: 48px;
    line-height: 40px;
    font-weight: 700;
    margin: 5px 0 20px 0;
}


ks-page-footer .newsletter ks-button {
    width: 100%;
}

ks-page-footer .portals {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    padding: 20px;
    border-top: solid 1px #2b2b2b
}

ks-page-footer .portals > div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    padding: 10px;
}

ks-page-footer .separator {
    width: 1px;
    height: 35px;
    margin: 0 20px;

    background-color: #2b2b2b;
}

ks-page-footer .software {
    background-color: var(--footer-color-darker);
    color: var(--footer-text-color-darker);
    font-size: 13px;
    text-align: center;

    padding: 10px;
}

ks-page-footer .software > a {
    color: var(--footer-text-color-darker);
}

@media only screen and (max-width: 1060px) {
    ks-page-footer .about {
        flex-direction: column;
        padding: 50px 30px 40px 30px;
    }


    ks-page-footer .contact {
        max-width: 220px;
    }

    ks-page-footer .links {
        display: flex;
        justify-content: center;
    }

    ks-page-footer .newsletter {
        margin-top: 40px;
        padding: 40px 0 0 0;

        border-top: solid 1px #2b2b2b;
    }
    
    ks-page-footer .newsletter ks-button {
        max-width: 290px;
    }
}

@media only screen and (max-width: 640px) {
    ks-page-footer .about {
        flex-direction: column;
        padding: 30px;
    }

    ks-page-footer .links {
        margin-top: 0px;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    ks-page-footer .links > * {
        margin-top: 40px;
        margin-right: 0px;
        padding: 0;
    }

    ks-page-footer .newsletter>div {
        font-size: 14px;
    }

    ks-page-footer .newsletter>div:last-of-type {
        font-size: 40px;
        margin-bottom: 10px;
    }
}