import { Component, h, Element, State, Prop, Method } from '@stencil/core';
import { common } from "../../global/data/common";
import { ProductCardData } from '../../global/data/listing';
import { commonDynamic } from '../../global/data/commonDynamic';
import Swiper from 'swiper';

@Component({
    tag: 'ks-product-suggestions',
    styleUrl: 'product-suggestions.css'
})
export class ProductSuggestions {
    @Element() root: HTMLElement;
    overlay: HTMLKsOverlayElement;
    carousel: Swiper;
    
    @Prop() suggestionHeading: string = "Produkty sugerowane";
    
    @State() name: string;
    @State() loading: boolean = true;
    @State() products = new Array<ProductCardData>();

    componentDidRender() {
        this.overlay = this.root.querySelector('ks-overlay');
    }

    @Method()
    async show(productId: string, name: string) {
        this.showCarousel();

        this.loading = true;
        this.name = name;
        this.products = [];
        
        this.overlay.show();

        setTimeout(async () => {
            const headers = new Headers();
            headers.append('pragma', 'no-cache');
            headers.append('cache-control', 'no-cache');
    
            let body = new FormData();
            body.append("id", productId);
            
            await fetch(commonDynamic.get("api").suggestions, {
                method: 'POST',
                body: body,
                 headers: headers,
                credentials: "same-origin"
            }).then(response => {
                return response.json();
            }).then(data => {
                this.products = data;
                this.loading = false;
            });
        }, 1500);

        
    }

    hide() {
        this.overlay.hide();
    }

    toCart() {
        window.location.href = 'koszyk.html';
    }

    showCarousel() {
        if(this.carousel == undefined) {
            this.carousel = new Swiper('.product-suggestions', {
                observer: true,
                observeParents: true,

                slidesPerView: "auto",
                centeredSlides: true,
                spaceBetween: 10,
                initialSlide: 1,

                preventInteractionOnTransition: true,
            });

            console.log(this.carousel);
        }
    }

	render() {
        const strings = common.get('translations');

		return <ks-overlay>
            <div class="content">
                <div class="top">
                    <div class="heading">Dodano do koszyka</div>
                    <div class="buttons">
                        <ks-button rounded secondary name={strings.suggestionGoToCart} onClick={()=>this.toCart()} />
                        <ks-button rounded name={strings.suggestionContinueBrowsing} onClick={()=>this.hide()} />
                    </div>
                </div>
                        
                <div class="bottom">

                    {this.loading ? null : <h3 class="suggestion-heading">{this.suggestionHeading}</h3>}
                    {this.loading ? <ks-loader dark large /> : null}

                    <div class="swiper-container product-suggestions" style={{display: this.loading ? "none" : "block"}}>
                        <div class="swiper-wrapper">
                            {this.products.map((product)=>
                                <div class="swiper-slide">
                                    <ks-product-card link-only product={product}/>
                                </div>
                            )}
                        </div>
                    </div>

                    <div class="fade-left"></div>
                    <div class="fade-right"></div>
                </div>
            </div>
        </ks-overlay>;
    }

}
