ks-overlay {
    display: block;
}
  
ks-overlay dialog {
    border: none;
    padding: 0;
    background: transparent;
    max-width: none;
    max-height: none;
    width: 100dvw;
    height: 100dvh;
    margin: 0;
    
    justify-content: center;
    align-items: center;
    
    overflow: hidden;
    color: var(--overlay-text);

    opacity: 0;
    transition: opacity 0.3s ease, overlay 0.3s ease allow-discrete, display 0.3s ease allow-discrete;
  }

  ks-overlay dialog[open] {
    display: flex;
    opacity: 1;
  }

  ks-overlay[dark] dialog::backdrop {
    backdrop-filter: var(--overlay-dark-filter);
    background-color: var(--overlay-dark-background);
  }
  
  ks-overlay[dark] dialog {
    color: var(--overlay-dark-text);
  }
  
  ks-overlay dialog::backdrop {
    backdrop-filter: var(--overlay-filter);
    background-color: var(--overlay-background);
    opacity: 0;
    transition: opacity 0.3s ease, overlay 0.3s ease allow-discrete, display 0.3s ease allow-discrete;
  }
  
  ks-overlay dialog[open]::backdrop {
    opacity: 1;
  }
  
  @starting-style {
    ks-overlay dialog[open] {
      opacity: 0;
    }
    
    ks-overlay dialog[open]::backdrop {
      opacity: 0;
    }
  }