import { Component, h, Method, Element, Prop, State, Event, EventEmitter } from '@stencil/core';

@Component({
	tag: 'ks-dialog',
	styleUrl: 'dialog.css'
})
export class dialog {
    @Element() root: HTMLElement;
    overlay: HTMLKsOverlayElement;

    @Prop({reflect: true}) nopadding: boolean;
    @Prop({reflect: true}) dark: boolean;
    @Prop({reflect: true}) smallmobile: boolean;
    @Prop({reflect: true}) closable: boolean = true;

    componentDidRender() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    @Method() async show() {
        this.overlay.show();
    }

    @Method() async hide() {
        this.overlay.hide();
    }

    @Event() closed: EventEmitter;

    @State() close: boolean = true;
    @State() loading: boolean = false;
    @State() success: boolean = false;
    @State() failure: boolean = false;

    @State() heading: string = "";
    @State() message: string = "";

    @Method() async showLoading() {
        this.loading = true;
        this.close = false;
    }

    @Method() async showSuccess(heading: string, message: string) {
        this.heading = heading;
        this.message = message;

        setTimeout(() => {
            this.success = true;
            this.loading = false
            this.close = true;
        }, 400);
    }

    @Method() async showFailure(heading: string, message: string) {
        this.heading = heading;
        this.message = message;

        setTimeout(() => {
            this.failure = true;
            this.loading = false
            this.close = true;
        }, 400);
    }

    closeHandler() {
        if(this.success || this.failure) {
            setTimeout(() => {
                this.success = false;
                this.failure = false;
            }, 400);
        }
    }

	render() {
		return <ks-overlay dark={this.dark} close={this.close && this.closable} onClosed={()=>this.closeHandler()}>
            <div class="content">
                {this.close && this.closable ? 
                    <button class="close" onClick={()=>this.hide()} aria-label="Zamknij panel">
                        <ks-icon name="x" size={1.3} aria-hidden />
                    </button>
                : null}

                <slot />

                {this.loading ? 
                    <div class="overlay">
                        <ks-loader large dark/>
                    </div>
                : null}

                {this.success || this.failure ? 
                    <div class="overlay opaque">
                        <div>
                            <ks-icon name={this.success ? "check" : "alert-triangle"} size={4} />
                            <p class="header">{this.heading}</p>
                            <p>{this.message}</p>
                        </div>
                    </div>
                : null}
            </div>
        </ks-overlay>
	}
}