ks-navbar-language-button {
    display: block;
    position: relative;
    height: 70px;
}

ks-navbar-language-button > .button {
    outline-style: none;
    text-decoration: none !important;
    min-width: 60px;

    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    color: var(--navbar-text-color) !important;
    transition: var(--transition-background-color);
}

ks-navbar-language-button > .button:hover {
    background-color: var(--navbar-color-hover);
}

ks-navbar-language-button > .button:active {
    background-color: var(--navbar-color-active);
}

ks-navbar-language-button > .name {
    position: absolute;
    bottom: -28px;
    z-index: 2147483649;

    width: 250%;
    left: -75%;

    font-size: 14px;
    text-transform: uppercase;
    text-align: center;
    color: var(--navbar-text-color);

    opacity: 0;
    transition: opacity 0.3s ease;
    user-select: none;
    pointer-events: none;
}

ks-navbar-language-button > .button:hover + .name {
    opacity: 1;
}

ks-navbar-language-button .flag {
    height: 100%;
    width: 100%;

    max-width: 30px;
    max-height: 30px;
    border-radius: 30px;
    overflow: hidden;

    border: solid 2px white;
    background-color: white;
}

ks-navbar-language-button .symbol {
    position: absolute;
    top: 0;
    right: 3px;
    min-width: 25px;
    height: 25px;
    padding: 0px 1px 0px 0px;
    margin-top: 8px;
    border-radius: 500px;
    box-sizing: border-box;

    background: #000000;
    color: var(--navbar-text-color);
    font-size: 16px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

ks-navbar-language-button .symbol.small {
    font-size: 14px;
}


ks-navbar-language-button[sidebar] {
    display: flex;
    flex-direction: row-reverse;
    height: 34px;
    cursor: pointer;
}

ks-navbar-language-button[sidebar] .button {
    background-color: transparent !important;
    min-width: 34px;
    transform: translateX(3px);
}

ks-navbar-language-button[sidebar] .flag {
    border: solid 2px black;
    background-color: black;
    line-height: 0;
}

ks-navbar-language-button[sidebar] .name {
    position: static;
    text-transform: none;
    opacity: 1;
    color: #000000;
    font-size: unset;
    text-align: unset;
}