ks-article-card {
    background-color: white;
    box-shadow: var(--card-shadow);
}

ks-article-card a {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

ks-article-card[flat] {
    border: #ededed 1px solid;
    border-radius: 6px;
    overflow: hidden;
    box-shadow: none;
}

ks-article-card .image {
    position: relative;
    padding-top: 50%;
    max-width: 100%;
    overflow: hidden;
}

ks-article-card .image > * {
    position: absolute;
    top: 0;
}


ks-article-card .info {
    padding: 15px;
    flex: 1 0 auto;

    font-size: .875rem;
    line-height: 1.5;
}


ks-article-card .info h3 {
    margin-bottom: 5px;
}

ks-article-card a {
    text-decoration: none !important;
    color: #151515;
}

ks-article-card .meta {
    font-size: .875rem;
    line-height: 1.4;
    color: #707070;
}

ks-article-card .meta ks-icon:not(:first-of-type) {
    margin-left: 15px;
}

@media screen and (min-width: 960px) and (max-width: 1200px) {
    ks-article-card[hide-on-odd] {
        display: none !important;
    }
}

