ks-product-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: stretch;
    padding: 0 5px;
    gap: 5px;
}

@media (min-width: 360px) and (max-width: 640px) {
    ks-product-container > * {
        width: 49%;
    }
}

@media (min-width: 640px) {
    ks-product-container {
        gap: 10px;
    }

    ks-product-container > * {
        width: 228px;
    }
}