import { Component, h, Prop } from '@stencil/core';
import { home } from '../../global/data/home';

@Component({
  tag: 'ks-homepage-products',
  styleUrl: 'homepage-products.css'
})
export class ProductContainer {
	@Prop({mutable: true, reflect: true}) active = 0;

	render() {
		const products = home.get("products");

		return [
			<div class="buttons">
				{products.map((entry, index) =>
					<this.Button index={index} name={entry.name} />
				)}
			</div>,

			<div class="content">
				<slot />
			</div>
		];
	}

	Button = ({index, name}) => <ks-button round
		name={name}
		border={this.active != index}
		transitionless={this.active == index} 
		onClick={()=>this.active = index}
	/>;
}
