import { Component, h, State, Method, Element } from '@stencil/core';
import { common } from "../../../global/data/common";
import { commonDynamic } from "../../../global/data/commonDynamic";

@Component({
    tag: 'ks-navbar-sidebar',
    styleUrl: 'navbar-sidebar.css'
})
export class NavbarSidebar {
    @Element() root: HTMLElement;
    @State() active: number = undefined;

    sidepanel: HTMLKsSidepanelElement;
    componentDidRender() {
        this.sidepanel = this.root.querySelector("ks-sidepanel");
    }

    toggled: boolean = false;
    @Method() async show() {
        this.sidepanel.show();
    }

    languagePanel() {
        this.sidepanel.hide();
        document.querySelector("ks-navbar-language-panel").show()
    }

    render() {
        const favouritesCount = commonDynamic.get("heartCount");

        const strings = common.get("translations");

        return <ks-sidepanel>
            <ul>
                <li class="header">{strings.navbarButtonMenu.toUpperCase()}</li>
                <li class="divider"></li>

                {!commonDynamic.get("loggedIn") ?        
                    <li><a href={common.get("loginLink")}>{strings.navbarButtonLogin} <ks-icon name="log-in"></ks-icon></a></li>
                : null}

                <li class="small">
                    <a href={common.get("heartLink")}>{strings.navbarButtonFav}
                        {favouritesCount && favouritesCount != 0 ? 
                            <span class="badge">{favouritesCount}</span> :
                            <ks-icon name="star"></ks-icon>
                        }
                    </a>
                </li>

                {commonDynamic.get("loggedIn") ? 
                    <li><a href={common.get("accountLink")}>
                        {strings.navbarButtonAccount} <ks-icon name="user"></ks-icon>
                    </a></li>
                : null}

                {commonDynamic.get("loggedIn") ?
                    <li><a href={common.get("logoutLink")}>
                        {strings.navbarButtonLogout} <ks-icon name="log-out"></ks-icon>
                    </a></li>
                : null}


                {common.get('languages').length > 1 ?
                    <li onClick={()=>this.languagePanel()}>
                        <ks-navbar-language-button sidebar name={strings.navbarButtonLanguage}/>
                    </li>
                : null}
                                            
                <li class="header">{strings.navbarCategories}</li>
                <li class="divider"></li>
            </ul>
            
            <ks-category-sidebar />
        </ks-sidepanel>
    }
}