ks-navbar-contact-panel .contents {
    width: 720px;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
}


ks-navbar-contact-panel .buttons  {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

ks-navbar-contact-panel iframe {
    width: 100%;
    height: 30vh;
}

ks-navbar-contact-panel .emphasis {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
}

@media only screen and (max-width: 720px) {
    ks-navbar-contact-panel .contents {
        max-width: 100vw;
        padding: 20px;
        box-sizing: border-box;
        text-align: center;
    }
}

