import { Component, h, Prop } from '@stencil/core';

@Component({
  tag: 'ks-homepage-info',
  styleUrl: 'homepage-info.css'
})
export class HomepageInfo {
    @Prop({mutable: true, reflect: true}) expanded: boolean = false;

    render() {
        return [
            <div class="main">
                <slot name="mainHeading" />
                <slot name="mainContent" />
            </div>,

            <div class="more">
                <div>
                    <slot name="firstHeading" />
                    <slot name="firstContent" />
                </div>

                <div>
                    <slot name="secondHeading" />
                    <slot name="secondContent" />
                </div>

                <div>
                    <slot name="thirdHeading" />
                    <slot name="thirdContent" />
                </div>
            </div>,

            <ks-button border round
                name={this.expanded ? "Zwiń" : "Rozwiń"}
                onClick={()=>this.expanded = !this.expanded}
            ></ks-button>
        ];
    }
}