import { Component, h, Listen } from '@stencil/core';
import { common } from '../../../global/data/common';


@Component({
  tag: 'ks-newsletter-side-button',
  styleUrl: 'newsletter-side-button.css'
})
export class NewsletterSideButton {
    @Listen('click')
    protected clickHandler() {
        document.querySelector('ks-newsletter-popup-edrone')?.Show()
    }

    render() {
        const strings = common.get('translations');
        
        return [
            <div class="label">{strings.newsletterSideLeft}</div>,
            <ks-icon name='mail' />,
            <div class="benefit">{strings.newsletterSideRight}</div>
        ];
    }
}
