import { Component, h, Prop, State, Watch, Element, Event, EventEmitter } from '@stencil/core';
import { window_load } from '../deferredpromise';
@Component({
    tag: 'ks-img',
    styleUrl: "img.css"
})
export class Img {
    @Prop({reflect: true}) src: string;
    @Prop() alt: string;
    
    @Prop() target: string;
    @Prop() observerMargin: string;
    @Prop() sync: boolean = false;

    @Prop() vertical: boolean = false;
    @Prop() contained: boolean = false;
    @Prop({reflect: true}) fill: boolean = false;
    @Prop() left: boolean = false;
    @Prop() right: boolean = false;
    @Prop() center: boolean = false;

    @Prop() width: number;
    @Prop() height: number;

    @Prop({reflect: true}) limit: boolean = false;

    @State() loaded: boolean = false;

    @Element() root: HTMLElement;

    @Event() lazyLoaded: EventEmitter;
    loadHandler(e: Event) {
        if (e.target instanceof HTMLElement && !e.target.getAttribute("src").includes('data:image')) {
            this.loaded = true;
            this.lazyLoaded.emit();
        }
    }
    
    @Watch('src') srcListener() {
        const image = this.root.querySelector("img");
        image.removeAttribute("src");
        image.setAttribute("src", this.src);

        setTimeout(()=>{
            if(!image.complete) {
                this.loaded = false;
            }
        }, 200);
    }

    private observer: IntersectionObserver;
    private image: HTMLImageElement;

    componentDidLoad() {
        if(!this.sync)
            window_load.promise.then(()=>this.initializeObserver());
    }

    initializeObserver() {
        this.image = this.root.querySelector('img') as HTMLImageElement;
        const target = this.target ? this.root.closest(this.target) : this.root;
        
        const onIntersection = (entries)=> {
            for (const entry of entries) {
                if (entry.isIntersecting) {
                    if (this.observer) {
                        this.observer.disconnect();
                    }
                    if (this.image.getAttribute('data-src')) {
                        this.image.setAttribute('src', this.image.getAttribute('data-src'));
                        this.image.removeAttribute('data-src');
                    }
                }
            }
        }

        if (this.image) {
            
            this.observer = new IntersectionObserver(onIntersection, {
                rootMargin: this.observerMargin
            });
            this.observer.observe(target);
        }

        this.SetAlignment();
    }

    SetAlignment() {
        const float =
            this.center ? "center" :
            this.right ? "right" :
            this.left ? "left" : "";

        if(float != "")
            this.root.classList.add(float);
    }

	render() {
        const responsive = this.contained ? "contained" :
            this.vertical ? "vertical" : "horizontal";

        const loading = this.loaded ? "" : "loading";

        const classes = [responsive].join(" ");

        const max = this.limit ? {maxWidth: `${this.width}px`} : null;

        if(this.sync) return (
            <img class={classes} src={this.src} alt={this.alt} width={this.width} height={this.height} style={max}/>
        );

        if(this.fill) return [
            <canvas width={this.width} height={this.height}></canvas>,

            (!this.loaded ? <ks-loader dark /> : null),
                
            <img class={classes + " " + loading} alt={this.alt}
                 onLoad={(e)=>this.loadHandler(e)} data-src={this.src}
                 width={this.width} height={this.height}
            />
        ];

        return [
            (!this.loaded ? <ks-loader dark /> : null),
            !this.loaded ? <canvas width={this.width} height={this.height} style={max}></canvas> : null,
                
            <img class={classes + " " + loading} alt={this.alt}
                 onLoad={(e)=>this.loadHandler(e)} data-src={this.src}
                 width={this.width} height={this.height}
                 style={max}
            />
        ];
	}
}
