import { Component, h, State, Method, Listen, Element, Event, EventEmitter } from '@stencil/core';
import { common } from '../../../global/data/common';
import { commonDynamic } from '../../../global/data/commonDynamic';
@Component({
    tag: 'ks-navbar',
    styleUrl: 'navbar.css'
})
export class Navbar {
    @Element() root: HTMLElement;

    @Event() navbarRendered: EventEmitter;
    componentDidRender() {
        this.navbarRendered.emit();
    }

    componentWillLoad() {
        this.resizeHandler();
    }

    @State() mobile: boolean = false;
    @Listen('resize', { target: 'window' })
    resizeHandler() {
        this.mobile = window.innerWidth < 960 ? true : false;
    }

    @Method() async IncrementCart(count: string = "1") {
        const countInt = parseInt(count);
        const incremented = commonDynamic.get("cartCount") + countInt;
        commonDynamic.set("cartCount", incremented);
    }

    @Method() async DecrementCart() {
        const count = commonDynamic.get("cartCount");
        if(count > 0)
            commonDynamic.set("cartCount", count-1);
    }

    @Method() async IncrementHeart() {
        const incremented = commonDynamic.get("heartCount") + 1;
        commonDynamic.set("heartCount", incremented);
    }

    @Method() async DecrementHeart() {
        const count = commonDynamic.get("heartCount");
        if(count > 0)
            commonDynamic.set("heartCount", count-1);
    }

    render() {
        const heartCount = commonDynamic.get("heartCount");
        const cartCount = commonDynamic.get("cartCount");
        const loaded = commonDynamic.get("loaded") ? "loaded" : null;

        const strings = common.get("translations");

        return [
            <nav>
                <div class="logo">
                    <div>
                        <a href="/">
                            <ks-img contained sync src={common.get("logo")} width={217} height={35} alt="logo kuchnia sklep.pl"></ks-img>
                        </a>
                        {common.get("promo") ? common.get("promoLink") && !this.mobile ? 
                            <a class="promo" href={common.get("promoLink")}>{common.get("promo")}</a> :
                            <span class="promo">{common.get("promo")}</span>
                        : null}
                    </div>
                </div>

                <div class="search">
                    <ks-navbar-search />
                </div>
                
                <div class={["buttons", loaded].join(' ')} id="ks-navbar-menu-buttons">
                    {common.get('languages').length > 1 ?
                        <ks-navbar-language-button name={strings.navbarButtonLanguage}
                            onClick={()=>this.root.querySelector("ks-navbar-language-panel").show()}
                        />
                    : null}

                    <ks-navbar-button id="navbar-button-contact" name={strings.navbarButtonContact} icon="phone"
                        onClick={()=>this.root.querySelector("ks-navbar-contact-panel").Toggle()}
                    />

                    {commonDynamic.get('loggedIn') && !commonDynamic.get('guest') ?
                        <ks-navbar-button id="navbar-button-account" name={strings.navbarButtonAccount} link={common.get('accountLink')} icon="user" />
                    : null}

                    <ks-navbar-button id="navbar-button-fav" name={strings.navbarButtonFav} link={common.get('heartLink')} icon="star" count={heartCount} />
                    <ks-navbar-button id="navbar-button-cart" name={strings.navbarButtonCart} link={common.get('cartLink')} icon="shopping-bag" count={cartCount} />

                    {!commonDynamic.get('loggedIn') ?
                        <ks-navbar-button last id="navbar-button-login" name={strings.navbarButtonLogin} link={common.get('loginLink')} icon="user" />
                    : null }

                    {commonDynamic.get('loggedIn') || commonDynamic.get('guest') ?
                        <ks-navbar-button last id="navbar-button-logout" name={strings.navbarButtonLogout} link={common.get('logoutLink')} icon="log-out" />
                    : null }

                    <ks-navbar-button id="navbar-button-menu" name={strings.navbarButtonMenu} icon="menu" onClick={()=>this.root.querySelector("ks-navbar-sidebar").show()} />
                </div>
                
                <ks-navbar-contact-panel />
                
                {common.get('languages').length > 1 ? <ks-navbar-language-panel /> : null}
            </nav>,

            <ks-navbar-categories/>,
            <ks-navbar-sidebar/>,
            <ks-navbar-search mobile/>
        ];
    }
}