ks-category-sidebar .category summary {
    list-style-type: none;
}

ks-category-sidebar .category > .name {
    position: relative;
    cursor: pointer;
    padding: 7px 0px 5px 0px;
}

ks-category-sidebar .category > .name > ks-icon {
    position: absolute;
    top: 6px;
    right: 0px;
}

ks-category-sidebar .category > .children {
    padding: 5px 0px 8px 10px;
    font-size: 16px;
}

ks-category-sidebar .category[open] > .children {
    animation: fade-in 0.3s ease;
}

ks-category-sidebar .category > summary > .minus { opacity: 0; }
ks-category-sidebar .category > summary > .plus { opacity: 1; }

ks-category-sidebar .category[open] > summary > .minus { opacity: 1; }
ks-category-sidebar .category[open] > summary > .plus { opacity: 0; }

ks-category-sidebar .category > .children > a {
    padding: 4px 0px;
}

ks-category-sidebar .seeall {
    color: #bbbbbb  !important;
}
