ks-img2 {
    display: inline-block;
    box-sizing: border-box;

    max-height: inherit;
    max-width: inherit;
    height: 100%;
    font-size: 0;
    vertical-align: middle;

    position: relative;
}

ks-img2 picture {
    width: inherit;
    height: inherit;
    max-height: inherit;
    max-width: inherit;
}

ks-img2 img {
    display: inline-block;
    width: auto;
    height : auto;
    max-width: inherit;
    max-height: inherit;
    font-size: 0;
    vertical-align: middle;

    opacity: 1;
    transition: opacity 0.3s ease;
}

ks-img2 canvas {
    display: inline-block;
    width: auto;
    height : auto;
    max-width: inherit;
    max-height: inherit;
    font-size: 0;
    vertical-align: middle;
}

ks-img2[src*=".svg"] img {
    display: inline-block;
    font-size: 0;
    vertical-align: middle;
    height: 100%;
    width: 100%;
    max-width: inherit;
    max-height : inherit;
    overflow: visible;

    opacity: 1;
    transition: opacity 0.3s ease;
}

ks-img2[vertical] img {
    width: auto;
}

ks-img2[horizontal] img {
    height: auto;
}

ks-img2 img.loading {
    opacity: 0;
    height: 0;
}

ks-img2[limit] img {
    max-width: 100%;
    max-height: 100%;
}

ks-img2[center] picture {
    margin: auto;
    display: inline-flex;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
}


ks-img2[fill] picture {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}