import { Component, h, Element, Prop, Host, Method, Build, State } from '@stencil/core';
import { common } from '../../global/data/common';
import { commonDynamic, ConsentItem } from '../../global/data/commonDynamic';
import { marked } from 'marked';

@Component({
  tag: 'ks-cookie-popup',
  styleUrl: 'cookie-popup.css'
})
export class CookiePopup{
    @Element() root: HTMLElement;

    @Prop({reflect: true, mutable: true}) hide: boolean = false;
    @Prop({reflect: true, mutable: true}) show: boolean = false;
    @Prop({reflect: true, mutable: true}) extend: boolean = false;
    @Prop({reflect: true, mutable: true}) requirement: boolean = false;

    @State() requirementMessage: string;

    extendpanel() {
        this.extend = !this.extend;
    }

    cookie(consent) {
        var expiration = "";
        var expirationDate = new Date();
        expirationDate.setMonth(expirationDate.getMonth() + 12 * 2);
        expiration = "expires=" + expirationDate.toUTCString() + "; ";

        document.cookie = "akceptCookie=tak; " + expiration + "path=/";

        const cookieName = commonDynamic.get('consent').cookie;
        document.cookie = `${cookieName}=${JSON.stringify(consent)};${expiration}path=/`;
    }

    getCookie(cookieName) {
        let cookie = {};
        document.cookie.split(';').forEach(function(el) {
            let [key,value] = el.split('=');
            cookie[key.trim()] = value;
        });

        if(cookie[cookieName])
            return JSON.parse(cookie[cookieName]);
        return false;
    }

    setStateFromCookie () {
        let consent = commonDynamic.get('consent');

        const consentCookie = this.getCookie(consent.cookie) as string[];
        consent.items = consent?.items?.map(item => {
            if(item.disabled) return item;

            item.checked = !!consentCookie?.find?.(value => value == item.inputname);
            return item;
        });

        commonDynamic.set("consent", consent);
    }

    componentWillLoad() {
        this.setStateFromCookie();

        if(document.cookie.indexOf('akceptCookie=tak') == -1 || document.cookie.indexOf(commonDynamic.get('consent').cookie) == -1)
            this.showpanel(true);
    }

    @Method()
    async showpanel(init: boolean = false) {
        this.setStateFromCookie();
        this.show = true;
        this.hide = true;
        setTimeout(() => {
            this.hide = false;
        }, init ? common.get('cookieDelay') : 100);
    }

    checkconsentpromise: () => void;

    @Method()
    async requireconsent(requirementName: string) : Promise<boolean> {
        this.checkconsentpromise = undefined;

        if(this.checkconsent(requirementName)) return true;

        let consent = commonDynamic.get("consent");
        const requirement = consent.requirements.find(entry => entry.name = requirementName);

        consent.items.forEach((entry, index) => {
            const consentRequired = requirement.consents.includes(entry.inputname);
            consent.items[index].highlighted = consentRequired;
        });

        this.requirement = true;
        this.extend = true;
        this.requirementMessage = requirement.message;

        commonDynamic.set("consent", consent);

        return new Promise<boolean>((resolve) => {
            this.showpanel();
            this.checkconsentpromise = () => resolve(this.checkconsent(requirementName));
        });
    }

    checkconsent(requirementName: string) {
        let requirementsMet = true;
        let consent = commonDynamic.get("consent");
        const requirement = consent.requirements.find(entry => entry.name = requirementName);

        consent.items.forEach((entry, index) => {
            const consentRequired = requirement.consents.includes(entry.inputname);
            if(consentRequired && !consent.items[index].checked)
                requirementsMet = false;
        });

        return requirementsMet;
    }

    hidepanel(all: boolean) {
        this.hide = true;

        const consent = this.consent(all);
        this.cookie(consent);
        this.setStateFromCookie();
        this.checkconsentpromise?.();
        this.registerGTMCallback(consent);

        setTimeout(() => {
            this.show = false;
            this.hide = false;
            this.requirement = false;
        }, 400);
    }

    registerGTMCallback(consent) {
        (window as any).googleConsentListeners.forEach((callback) => {
            callback(consent);
        });
    }

    consent(all = false) {
        if(all) return commonDynamic.get("consent").items.map(entry => entry.inputname);

        const inputs = document.querySelectorAll("ks-cookie-popup input[type=checkbox]") as NodeListOf<HTMLInputElement>;
        return Array.from(inputs)
            .map(input => input.checked ? input.name : null)
            .filter(value => value);
    }

    @Method()
    async showinfo() {
        const details = document.querySelector("#cookie-popup-details") as HTMLKsDialogElement;
        details.show();
    }

    render() {
        if (!Build.isBrowser)
            return <Host/>;

        const consent = commonDynamic.get('consent');
        const cookieMessage = marked(this.requirement ? this.requirementMessage : common.get('cookieMessage'));

        return <Host aria-hidden={this.show ? "false" : "true"} role="dialog">
            <div class="message">
                <div innerHTML={cookieMessage}></div>
                {this.requirement ? null : <div class="buttons">
                    <ks-button round border light
                        name={consent.buttons.details}
                        onClick={()=>this.showinfo()}>
                    </ks-button>
                    <ks-button round border light
                        name={consent.buttons.settings}
                        onClick={()=>this.extendpanel()}>
                    </ks-button>
                    <ks-button round light
                        name={consent.buttons.acceptall}
                        onClick={()=>this.hidepanel(true)}>
                    </ks-button>
                </div>}
            </div>
            <div class="settings">
                {consent?.items?.map?.(item => this.type(item))}

                <div class="buttons">
                    <ks-button round border light
                        disabled={!this.extend}
                        name={consent.buttons.save}
                        onClick={()=>this.hidepanel(false)}>
                    </ks-button>

                    {this.requirement ? <ks-button round light
                        disabled={!this.extend}
                        name={consent.buttons.acceptall}
                        onClick={()=>this.hidepanel(true)}>
                    </ks-button> : null}
                </div>
            </div>
        </Host>;
    }

    type(item: ConsentItem) {
        return <div class={"type" + (item.highlighted ? " highlight" : "")}>
            <div class="heading">
                <div class="name">{item.name}</div>
                <label class="check" aria-label={item.name}>
                    <input type="checkbox" checked={item.checked} disabled={item.disabled || !this.extend} name={item.inputname} />
                    <span class="slider"></span>
                </label>
            </div>
            <div>{item.description}</div>
        </div>
    }
}
