import { Component, h, Element, Prop, State, Listen } from '@stencil/core';
import { commonDynamic } from '../../../global/data/commonDynamic';
import { common } from '../../../global/data/common';

@Component({
    tag: 'ks-navbar-search',
    styleUrl: "navbar-search.css"
})
export class NavbarSearch {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) mobile: boolean;
    @State() active: boolean = false;
    @State() select: number = -1;
    @State() items;
    data;
    maxCount = 6;

    async getData() {
        const language = common.get("language");
        const sessionName = `navbar-autocomplete-${language.id}`;
        const sessiondateName = `navbar-autocomplete-date-${language.id}`;

        const session = sessionStorage.getItem(sessionName);
        const sessiondate = sessionStorage.getItem(sessiondateName);
        const now = new Date().getTime();

        if (session && sessiondate) {
            const time = (now - parseInt(sessiondate)) / 1000 / 60;

            if(time < 5) {
                this.data = JSON.parse(sessionStorage.getItem(sessionName));
                return;
            }
        }

        const headers = new Headers();
		headers.append('pragma', 'no-cache');
		headers.append('cache-control', 'no-cache');

        this.data = await fetch(commonDynamic.get("api").searchAutocomplete, {
			method: 'GET',
			headers: headers,
			credentials: "same-origin"
		})
        .then(response => response.json());

        sessionStorage.setItem(sessionName, JSON.stringify(this.data));
        sessionStorage.setItem(sessiondateName, now.toString());
    }

    submit(e: Event) {
        if(this.select >= 0) {
            e.preventDefault();
            window.location.href = this.items[this.select].url;
            return;
        }

        const form = this.root.querySelector("form") as HTMLFormElement;
        if(form.checkValidity())
            form.submit();
    }

    async focus() {
        if(this.mobile) this.root.scrollIntoView({
            "behavior": "smooth"
        });
        
        if(this.data) {
            this.active = true;
            return;
        }

        await this.getData();
        this.active = true;
    }

    blurenabled = true;
    blur() {
        if(this.blurenabled)
            this.active = false;
    }

    @Listen("mousedown") clickListener(){
        this.blurenabled = false;
        setTimeout(() => this.blurenabled = true, 0);
    }

    input() {
        const value = this.inputElement.value.toLowerCase();

        if(value !== undefined && value.length < 3) {
            this.items = undefined;
            return;
        }

        let items = new Array();
        
        const condition = (k, v)=>{
            const wordpositions = k.split(" ").map(word=>k.indexOf(word))
            if(wordpositions.includes(k.toLowerCase().indexOf(v)))
                return true;
        };

        for(const key in this.data.manufacturers) {
            if(condition(key, value)) {
                items.push({
                    "name": key,
                    "url": this.data.manufacturers[key],
                    "type": "manufacturer"
                });
            }

            if(items.length >= this.maxCount) break;
        }

        if(items.length < this.maxCount) {
            for(const key in this.data.categories) {
                if(condition(key, value)) {
                    items.push({
                        "name": key,
                        "url": this.data.categories[key],
                        "type": "category"
                    });
                }
                if(items.length >= this.maxCount) break;
            }
        }

        if(items.length <= this.select)
            this.select = -1;

        this.items = items;
    }

    key(e: KeyboardEvent) {
        if(e.key == "ArrowDown") {
            e.preventDefault();

            if(this.select < this.items.length -1)
                this.select++;
        }

        else if(e.key == "ArrowUp") {
            e.preventDefault();

            if(this.select >= 0)
                this.select--;
        }
    }

    inputElement: HTMLInputElement;
    componentDidLoad() {
        this.inputElement = this.root.querySelector('input[name=szukaj]');
    }

    render() {
        return (
            <form method="post" action="szukaj.html" onSubmit={e=>this.submit(e)}>
                <a onClick={e=>this.submit(e)}>
                    <ks-icon name="search" size={1.1}></ks-icon>
                </a>
                <input aria-label="Szukaj produktów" name="szukaj" type="search" autocomplete="off" required
                    onFocus={()=>this.focus()}
                    onBlur={()=>this.blur()}
                    onInput={()=>this.input()}
                    onKeyDown={e=>this.key(e)}
                />

                <input type="hidden" name="postget" value="tak" />

                {this.active && this.items && this.items.length > 0 ?
                    <div class="list">
                        {this.items?.map((item, index) => 
                            <a href={item.url}
                                class={index == this.select ? "active" : ""}>
                                {item.name}
                                <ks-icon name={item.type == "category" ? "layers" : "bookmark"} size={0.9}/>
                            </a>
                        )}
                    </div>
                : null}
            </form>
        );
    }
}