import { Component, h, Prop, Element, Host } from '@stencil/core';
import feather from 'feather-icons';
import { recycle } from './recycle';

@Component({
    tag: 'ks-icon',
    styleUrl: 'icon.css'
})
export class Icon {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) name: string = "alert-octagon";
    @Prop() size: number = 1;

    svg: string;
    attrs: {[key: string]: string | number};

    componentWillLoad() {
        this.componentWillUpdate();
    }

    componentWillUpdate() {
        const name = this.name == "recycle" ? "alert-octagon" : this.name;

        const icon = feather.icons[name];
        this.svg = icon.contents;
        this.attrs = icon.attrs;

        this.root.style.lineHeight = `${this.attrs.width as number * this.size}px`;

        if(this.name == "recycle")
            this.svg = recycle;
    }

    render() {
        if(!this.attrs) return;

        let stroke = "";

        if( this.size >= 1.3 && this.size < 2 )
            stroke = "medium";

        else if (this.size >= 2)
            stroke = "thin";

        const width = this.attrs.width as number * this.size;
        const height = this.attrs.height as number * this.size;

        var svg = document.createElement('svg');
        svg.setAttribute("xmlns", this.attrs.xmlns as string);
        svg.setAttribute("width", `${width}`);
        svg.setAttribute("height", `${height}`);
        svg.setAttribute("viewBox", `0 0 ${this.attrs.width} ${this.attrs.height}`);
        svg.setAttribute("class", stroke);
        svg.innerHTML = this.svg;

        return <Host role="img" innerHTML={svg.outerHTML}></Host>;
    }
}


