import { Component, Host, h } from '@stencil/core';
import { common } from '../../../global/data/common';

@Component({
    tag: 'ks-top-banner',
    styleUrl: 'top-banner.css'
})
export class TopBanner {

    render() {
        const notice = common.get("topNotice");
        if(!notice || Object.keys(notice).length <= 0) return <Host hidden />;

        const content = [
            notice.image ? <ks-img2 vertical sync src={notice.image} alt={notice.name} aria-hidden="true"/> : null,
            <span>{notice.message}</span>
        ];

        const ariaLabel = `Banner informujący - ${notice.message}`;

        return <Host style={{backgroundColor: notice.color, color: notice.textColor}}>
            {notice.link ?
                <a class="container" href={notice.link} aria-label={ariaLabel}>
                    {content}
                </a> :
                <div class="container" aria-label={ariaLabel}>{content}</div>
            }
        </Host>;
    }
}