ks-cookie-popup {
    display: flex;
    flex-direction: column;
    align-items: center;

    position: fixed;
    overflow: hidden;

    font-family: var(--font-emphasis);

    bottom: 0px;
    left: 0px;
    right: 0px;

    z-index: 10000000;
    max-width: 100vw;
    
    background-color: rgba(0, 0, 0, 0.90);
    backdrop-filter: var(--overlay-dark-filter);
    border-top: solid 1px #000000;
    color: white;

    transition: transform 0.4s ease;
}

ks-cookie-popup .message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 160px;
    padding: 15px;
    box-sizing: border-box;

    text-align: center;
    font-size: 15px;
    line-height: 17px;
}

ks-cookie-popup .message a {
    text-decoration: underline;
    color: white !important;
}

ks-cookie-popup .message p {
    margin: 0 0 14px 0;
}

ks-cookie-popup[requirement] .message p {
    background-color: #ff4242;
    border-radius: 50px;
    padding: 10px 15px;
}

ks-cookie-popup .settings {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;

    height: 500px;
    max-width: 800px;
    padding: 0px 15px 15px 15px;
    box-sizing: border-box;

    font-size: 14px;
    line-height: 14px;
}

ks-cookie-popup .settings ks-button {
    margin-top: 10px
}

ks-cookie-popup:not([hide]):not([extend]) {
    transform: translateY(500px);
}

ks-cookie-popup:not([hide])[extend] {
    transform: translateY(0px);
}

ks-cookie-popup[hide] {
    transform: translateY(670px);
}

ks-cookie-popup:not([show]) {
    transform: translateY(600px);
    display: none;
}

ks-cookie-popup p {
    margin: 0;
    max-width: 100%;
}

ks-cookie-popup .buttons {
    display: flex;
    gap: 5px;
    flex-wrap: wrap;
    justify-content: center;
}

ks-cookie-popup .buttons > * {
    width: max-content;
}

ks-cookie-popup .message .buttons {
    margin: 0 0 0 20px;
}

ks-cookie-popup .settings {
    max-width: 800px;
}

ks-cookie-popup .type {
    margin-bottom: 10px;
}

ks-cookie-popup .name {
    font-size: 18px;
    font-weight: 700;
}

ks-cookie-popup .heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}


/* The switch - the box around the slider */
ks-cookie-popup .check {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 26px;
}
  
ks-cookie-popup .check input {
    opacity: 0;
    width: 0;
    height: 0;
}

ks-cookie-popup .check input:focus ~ .slider {
    outline: #ccc 2px solid;
    outline-offset: 2px;
}

ks-cookie-popup .check .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    border-radius: 34px;

    background-color: #ccc;
    transition: background-color ease .4s;
}

ks-cookie-popup .check .slider:before {
    position: absolute;
    content: "";
    height: 20px;
    width: 20px;
    left: 4px;
    bottom: 3px;
    border-radius: 50%;

    background-color: #242424;
    transition: 
        background-color ease .4s,
        transform ease .2s,
    ;
}

ks-cookie-popup .check input:disabled + .slider:before {
    background-color: #8d8d8d;
}

ks-cookie-popup .check input:checked + .slider:before {
    transform: translateX(22px);
}

ks-cookie-popup[requirement] .type.highlight .check .slider {
    background-color: #ff6f6f;
}

ks-cookie-popup[requirement] .type.highlight .check .slider:before {
    background-color: #8d1515;
}

ks-cookie-popup[requirement] .type.highlight .check input:checked + .slider {
    background-color: #87ff6f;
}

ks-cookie-popup[requirement] .type.highlight .check input:checked + .slider:before {
    background-color: #198d15;
}

@media (max-width: 700px) {
    ks-cookie-popup .message {
        font-size: 14px;
    }

    ks-cookie-popup .message .buttons {
        margin: 0
    }

    ks-cookie-popup .settings {
        justify-content: flex-start;
        max-width: 100vw;
    }

    ks-cookie-popup[requirement] .message {
        height: 120px;
    }

    ks-cookie-popup[requirement][hide] {
        transform: translateY(560px);
    }
    
    ks-cookie-popup[requirement]:not([show]) {
        transform: translateY(560px);
    }
}


@media (max-height: 700px) {
    ks-cookie-popup .settings {
        height: 400px;
        justify-content: flex-start;
    }
    
    
    ks-cookie-popup:not([hide]):not([extend]) {
        transform: translateY(400px);
    }
    
    ks-cookie-popup[hide] {
        transform: translateY(520px);
    }
    
    ks-cookie-popup:not([show]) {
        transform: translateY(520px);
        display: none;
    }
}

@media (max-height: 600px) {
    ks-cookie-popup .settings {
        height: 300px;
    }
    
    ks-cookie-popup:not([hide]):not([extend]) {
        transform: translateY(300px);
    }
    
    ks-cookie-popup[hide] {
        transform: translateY(420px);
    }
    
    ks-cookie-popup:not([show]) {
        transform: translateY(420px);
    }
}

@media (max-height: 540px) {
    ks-cookie-popup .settings {
        height: 200px;
    }
    
    ks-cookie-popup:not([hide]):not([extend]) {
        transform: translateY(200px);
    }
    
    ks-cookie-popup[hide] {
        transform: translateY(320px);
    }
    
    ks-cookie-popup:not([show]) {
        transform: translateY(320px);
    }
}

@media (max-width: 460px) {
    ks-cookie-popup .buttons > * {
        flex: 1 0 40%;
    }

    ks-cookie-popup .message {
        height: 240px;
    }

    ks-cookie-popup[requirement][hide] {
        transform: translateY(440px);
    }
    
    ks-cookie-popup[requirement]:not([show]) {
        transform: translateY(440px);
    }
}