ks-top-banner {
    display: block;
    position: relative;
    padding: 10px 10px;

    font-family: var(--font-emphasis);
    font-size: 15px;
    font-weight: 700;
    line-height: 15px;
    user-select: none;

    transition: var(--transition-background-color);
}

ks-top-banner[hidden] {
    display: none;
}

ks-top-banner .container {
    display: flex;
    justify-content: center;
    align-items: center;

    color: inherit !important;
    text-decoration: none !important;
    text-align: center;
}

ks-top-banner ks-img2 {
    width: auto;
    height: 35px;
    margin: -8px 3px -8px 0px;
}

@media only screen and (max-width: 959px) {
    ks-top-banner {
        font-size: 14px;
        line-height: 14px;
    }
}

@media only screen and (max-width: 480px) {
    ks-top-banner {
        font-size: 13px;
        line-height: 13px;
    }
}