import { Component, h, Method, Element } from '@stencil/core';
import { commonDynamic } from '../../../../global/data/commonDynamic';
import { common, Language, Currency } from '../../../../global/data/common';
import Fetch from '../../../../global/functions/fetch';

@Component({
    tag: 'ks-navbar-language-panel',
    styleUrl: 'navbar-language-panel.css'
})
export class NavbarLanguagePanel {
    @Element() root: HTMLElement;
    
    sidepanel: HTMLKsSidepanelElement;
    componentDidRender() {
        this.sidepanel = this.root.querySelector("ks-sidepanel");
    }

    toggled: boolean = false;
    @Method() async show() {
        this.sidepanel.show();
    }

    languageChange(language: Language) {
        if(common.get('languageDomainChange')) {
            var url = new URL(document.location.href);
            url.port = "";
            url.hostname = language.domain;
            document.location.href = url.href;
            return;
        }

        const data = new FormData();
        data.append("id", language.id.toString());

        Fetch(commonDynamic.get('api').changeLanguage, data)
            .then(() => {
                const href = document.location.href;

                if(href.includes(".html"))
                    document.location.href = href.substring(0, href.indexOf(".html") + 5);
                else document.location.reload();
            });
    }

    currencyChange(currency: Currency) {

        const data = new FormData();
        data.append("id", currency.id.toString());

        Fetch(commonDynamic.get('api').changeCurrency, data)
            .then(() => document.location.reload());
    }

    render() {
        const strings = common.get('translations');

        const languages = common.get('languages');
        const activeLanguage = common.get('language').id;

        const currencies = common.get('currencies');
        const activeCurrency = common.get('currency').id;

        return <ks-sidepanel>
            <div class="title">{strings.panelLanguage}</div>

            {languages.map(language => 
                <div class={language.id == activeLanguage ? "language active" : "language"}
                    onClick={() => this.languageChange(language)}
                >
                    <div class="name">{language.name}</div>
                    <div class="flag">
                        <ks-img2 src={language.flag}/>
                    </div>
                </div>
            )}

            <div class="title">{strings.panelCurrency}</div>

            {currencies.map(currency => 
                <div class={currency.id == activeCurrency ? "currency active" : "currency"}
                    onClick={() => this.currencyChange(currency)}
                >
                    <div class="name">{currency.name}</div>
                    <div class="tag">
                        <div class="code">{currency.code}</div>
                        <div class="symbol">{currency.symbol}</div>
                    </div>
                </div>
            )}
        </ks-sidepanel>;
    }
}